import { z } from "zod";

export const SelectionSchema = z.object({
  type: z.string().optional().nullable(),
  artist: z.array(z.string()).optional().nullable(),
  style: z.array(z.string()).optional().nullable(),
  lighting: z.string().optional().nullable(),
  detail: z.string().optional().nullable(),
  techniques: z.string().optional().nullable(),
  color: z.string().optional().nullable(),
  camera: z.string().optional().nullable(),
  resolution: z.string().optional().nullable(),
  emotion: z.string().optional().nullable(),
});

export const PromptMockupSchema = z.object({
  id: z.number(),
  prompt_id: z.number(),
  prompt_result_id: z.number(),
  product_id: z.number(),
  filename: z.string(),
});

export const PromptLikeSchema = z.object({
  id: z.number(),
  prompt_id: z.number(),
  user_id: z.string(),
});

export const PromptResultSchema = z.object({
  id: z.number(),
  prompt_id: z.number(),
  filename: z.string(),
  designfile: z.string().optional().nullable(),
  nobgfile: z.string().optional().nullable(),
  thumbfile: z.string().optional().nullable(),
  mockup: z.array(PromptMockupSchema).optional().nullable(),
  featured: z.boolean().default(false),
});

export const PromptSchema = z.object({
  id: z.number(),
  uuid: z.string().uuid(),
  created_at: z.string(),
  created_by: z.string().uuid().optional(),
  prompt: z.string(),
  selections: SelectionSchema.optional(),
  results: z.array(PromptResultSchema), // removed optional
  likes: z.array(PromptLikeSchema).optional(),
  like_count: z.array(z.object({ likes: z.number() })),
  is_liked: z.array(z.object({ user_id: z.string() })).optional(),
  session_key: z.string().optional(),
});

export const NewPromptSchema = PromptSchema.pick({
  prompt: true,
  selections: true,
  created_by: true,
});

export type PromptRecord = z.infer<typeof PromptSchema>;
export type PromptResult = z.infer<typeof PromptResultSchema>;
export type NewPromptRecord = z.infer<typeof NewPromptSchema>;
export type PromptMockup = z.infer<typeof PromptMockupSchema>;
export type PromptLike = z.infer<typeof PromptLikeSchema>;
