import { MultiSelect, Radio, Select } from "@mantine/core";
import { useState } from "react";
import { styleTypes } from "~/lib/promptGenerator";
import { useFormContext } from "./FormProvider";

export default function StepStyle() {
  const form = useFormContext();
  const [artStyleType, setArtStyleType] = useState<string>("artist");

  const { setFieldValue } = form;

  const changeArtStyle = (type: string) => {
    const opts = ["artist", "style", "techniques"];
    const update = opts.filter((o) => o !== type);
    update.forEach((u) => setFieldValue(u, null));
    setArtStyleType(type);
  };

  const { artist, lighting, detail, artStyle, techniques } = styleTypes;

  return (
    <>
      <h2 className="font-black tracking-tighter text-2xl">Choose Style</h2>
      <p className="text-gray-600">
        The art style plays a huge factor in the rendition, we like to think of
        style in three sub-categories: Lighting, Detail, and Art styles.
      </p>
      <div className="grid md:grid-cols-2 gap-4 mt-4">
        <div>
          <Select
            label="Lighting"
            multiple
            searchable
            clearable
            placeholder="Choose One"
            data={lighting}
            {...form.getInputProps("lighting")}
          />
        </div>

        <div>
          <Select
            label="Detail"
            multiple
            searchable
            clearable
            placeholder="Choose One"
            data={detail}
            {...form.getInputProps("detail")}
          />
        </div>

        <div>
          <Radio.Group
            value={artStyleType}
            onChange={(val) => changeArtStyle(val)}
          >
            <Radio value="artist" label="Style by Artist" />
            <Radio value="style" label="Style by Historical Era" />
            <Radio value="techniques" label="Style by Technique and Material" />
          </Radio.Group>
        </div>
        <div>
          {artStyleType === "artist" && (
            <MultiSelect
              label="Artists"
              multiple
              searchable
              clearable
              placeholder="Choose one or more"
              data={artist}
              {...form.getInputProps("artist")}
            />
          )}

          {artStyleType === "style" && (
            <Select
              label="Historical Era"
              multiple
              searchable
              clearable
              placeholder="Choose one"
              data={artStyle}
              {...form.getInputProps("style")}
            />
          )}

          {artStyleType === "techniques" && (
            <Select
              label="Technique and Material"
              multiple
              searchable
              clearable
              placeholder="Choose one"
              data={techniques}
              {...form.getInputProps("techniques")}
            />
          )}
        </div>
      </div>
    </>
  );
}
