import type { PostgrestError } from "@supabase/supabase-js";
import { useQuery } from "@tanstack/react-query";
import type { QueryOptions } from "@tanstack/react-query";
// import { useAtomValue } from "jotai";

// import { accessTokenAtom } from "~/lib/atoms/root";
import { getSupabase } from "~/lib/supabase";
import type { PromptRecord } from "./schema";
import { TABLE_NAME, SELECT } from "./const";
import { useAtomValue } from "jotai";
import { userAtom } from "~/lib/atoms/root";
// import { promptsAtom } from "~/lib/atoms/prompts";

export async function getPrompt({
  uuid,
  user_id,
  accessToken,
}: {
  uuid: string;
  user_id?: string;
  accessToken?: string;
}) {
  const client = getSupabase(accessToken);
  const query = client
    .from(TABLE_NAME)
    .select(`${SELECT}, is_liked:prompt_likes(user_id)`)
    .match({ uuid });

  if (user_id) {
    query.eq("is_liked.user_id", user_id);
  }

  const { data, error } = await query.single();

  if (error) throw error;

  return data as PromptRecord;
}

export function useGetPrompt(uuid?: string | null, options: any = {}) {
  // const accessToken = useAtomValue(accessTokenAtom);
  const user = useAtomValue(userAtom);
  // const setPrompts = useSetAtom(promptsAtom);

  return useQuery(
    ["prompts", uuid],
    () => getPrompt({ uuid: uuid!, user_id: user?.id }),
    {
      ...options,
      enabled: Boolean(uuid),
    }
  );
}
