import { ActionIcon, Button, Menu, Text } from "@mantine/core";
import { Link } from "@remix-run/react";
import { DotsVertical, Heart, Trash } from "tabler-icons-react";
import { classNames } from "~/lib/classNames";
import { useGetPrompt, useDeletePrompt } from "~/modules/prompts";
import type { PromptRecord } from "~/modules/prompts";
import CardCarousel from "./CardCarousel";
import { useRevalidate } from "~/lib/hooks/useRevalidate";
import { useConfirmationModal } from "~/lib/hooks/useConfirmationModal";

export interface ProductCardProps {
  prompt: PromptRecord;
}

export default function PromptCardOwner({
  prompt: initialPrompt,
}: ProductCardProps) {
  const revalidator = useRevalidate("/app");
  const { openConfirmModal, modal: ConfirmationModal } = useConfirmationModal();

  const { data: prompt } = useGetPrompt(initialPrompt.uuid, {
    initialData: initialPrompt,
  });
  const deletePrompt = useDeletePrompt({
    onSuccess: () => {
      revalidator.revalidate();
    },
  });

  if (!prompt) return null;

  return (
    <div className="bg-white rounded-lg shadow-xl p-4" key={prompt.id}>
      <div className="overflow-hidden rounded-lg aspect-1 bg-black/50 backdrop-blur-md relative">
        {prompt.results && prompt.results.length > 0 ? (
          <CardCarousel
            navigate={`/app/${prompt.uuid}`}
            prompt={prompt}
            images={prompt.results}
          />
        ) : (
          <Link
            to={`/app/${prompt.uuid}`}
            className="h-full w-full flex flex-col items-center justify-center bg-gray-50"
          >
            <p className="text-xl font-black tracking-tighter mb-4">
              No Images Generated
            </p>
          </Link>
        )}
      </div>
      <div className="relative p-4 flex flex-col">
        <Link to={`/app/${prompt.uuid}`} className="block text-lg mb-2">
          Prompt #{prompt.id}
        </Link>
        <Text mb={12} lineClamp={3}>
          {prompt.prompt}
        </Text>
        <div className="flex items-center justify-between mt-auto">
          <Button
            component={Link}
            variant="gradient"
            gradient={{ from: "violet", to: "pink" }}
            radius="xl"
            to={`/app/${prompt.uuid}`}
          >
            View
          </Button>
          <Menu position="bottom-end">
            <Menu.Target>
              <ActionIcon radius="xl" size="lg" variant="subtle" color="violet">
                <DotsVertical size={16} />
              </ActionIcon>
            </Menu.Target>
            <Menu.Dropdown>
              <Menu.Item
                color="red"
                icon={<Trash size={16} />}
                onClick={() => openConfirmModal()}
              >
                Delete
              </Menu.Item>
            </Menu.Dropdown>
          </Menu>
        </div>
      </div>
      <ConfirmationModal
        title="Please confirm your action"
        onConfirm={() => {
          //
          deletePrompt.mutate({ uuid: prompt.uuid });
        }}
      >
        <p className="text-sm">
          Are you sure you want to delete this prompt? This action cannot be
          undone.
        </p>
      </ConfirmationModal>
    </div>
  );
}
