import type { ActionArgs, LoaderArgs } from "@remix-run/node";
import { json } from "@remix-run/node";
import { useNavigate } from "@remix-run/react";
import { useEffect, useState } from "react";
import { requireAuthSession } from "~/lib/auth/guards.server";
import { PROMPT_SESSION_KEY } from "~/lib/const";
import { commitSession, getSession } from "~/lib/session.server";
import { getPrompt, SelectionSchema, useCreatePrompt } from "~/modules/prompts";
import { STEP_ARRAY } from "~/components/steps";
import { Button, Textarea, Tooltip } from "@mantine/core";
import { RefreshDot, Windmill } from "tabler-icons-react";

import { FormProvider, useForm } from "~/components/steps";
import { z } from "zod";
import { zodResolver } from "@mantine/form";
import { composePrompt } from "~/lib/promptGenerator";
import { useGenerateImage } from "~/lib/hooks/useGenerateImage";

export async function loader({ request }: LoaderArgs) {
  const { accessToken } = await requireAuthSession(request);
  const session = await getSession(request);

  // if (!session.has(PROMPT_SESSION_KEY)) {
  //   const prompt = await createPrompt({
  //     values: {
  //       prompt: "",
  //       selections: wizardFormDefaultState,
  //       created_by: userId,
  //     },
  //     accessToken,
  //   });
  //   session.set(PROMPT_SESSION_KEY, prompt.uuid);

  //   return json(
  //     {
  //       prompt,
  //     },
  //     {
  //       headers: {
  //         "Set-Cookie": await commitSession(session),
  //       },
  //     }
  //   );
  // }

  if (session.has(PROMPT_SESSION_KEY)) {
    const uuid = session.get(PROMPT_SESSION_KEY);
    const prompt = await getPrompt({
      uuid,
      accessToken,
    });

    return json({
      prompt,
    });
  }

  return null;
}

export async function action({ request }: ActionArgs) {
  await requireAuthSession(request);
  const session = await getSession(request);
  const data = Object.fromEntries(await request.formData());

  if (data.uuid) {
    session.set(PROMPT_SESSION_KEY, data.uuid as string);

    return json(
      {},
      {
        headers: {
          "Set-Cookie": await commitSession(session),
        },
      }
    );
  }

  return null;
}

const PromptFormSchema = SelectionSchema.extend({
  prompt: z.string(),
  description: z
    .string()
    .optional()
    .nullable()
    .transform((val, ctx) => {
      if (val && val.split(" ").length < 3) {
        ctx.addIssue({
          code: z.ZodIssueCode.custom,
          message: "Must be greater than 3 words",
        });
        return z.NEVER;
      }
      return val;
    }),
});

enum PromptMode {
  LIVE = "live",
  WIZARD = "wizard",
}

export default function CreateLayout() {
  // const { prompt } = useLoaderData<{ prompt: PromptRecord }>();
  const [promptValue, setPromptValue] = useState<string>("");
  const [mode, setMode] = useState<PromptMode>(PromptMode.WIZARD);
  const createPrompt = useCreatePrompt();
  const generateImage = useGenerateImage();
  const navigate = useNavigate();

  const form = useForm({
    validate: zodResolver(PromptFormSchema),
    initialValues: {
      prompt: "",
      description: "",
      type: null,
      artist: null,
      style: null,
      lighting: null,
      detail: null,
      techniques: null,
      color: null,
      camera: null,
      resolution: null,
      emotion: null,
    },
  });

  useEffect(() => {
    setPromptValue(composePrompt(form.values));
  }, [form.values]);

  const onSubmit = (values: typeof form.values) => {
    const { prompt, description: _, ...selections } = values;
    createPrompt.mutate(
      {
        prompt: promptValue,
        selections,
      },
      {
        onSuccess: (data) => {
          generateImage.mutate(
            {
              promptId: data.id,
              imageCount: 4,
            },
            {
              onSuccess: () => {
                navigate(`/app/${data.uuid}`);
              },
            }
          );
        },
      }
    );
  };

  const isLoading = createPrompt.isLoading || generateImage.isLoading;

  return (
    <FormProvider form={form}>
      <form
        className="container max-w-screen-xl md:grid md:grid-cols-3 md:gap-12 md:my-24"
        onSubmit={form.onSubmit(onSubmit)}
      >
        <div className="">
          <div className="sticky top-6">
            <Textarea
              label="Prompt"
              size="xl"
              radius="lg"
              description="As you make selections, the prompt will be updated and optimized"
              minRows={3}
              mb={16}
              autosize
              value={promptValue}
              onFocus={() => setMode(PromptMode.LIVE)}
              onBlur={() => setMode(PromptMode.WIZARD)}
              onChange={(e) => {
                if (mode === PromptMode.LIVE) {
                  setPromptValue(e.target.value);
                } else {
                  form.setFieldValue("prompt", e.target.value);
                }
              }}
            />
            <div className="flex items-center justify-between mb-4">
              <Button
                variant="gradient"
                gradient={{ from: "violet", to: "pink" }}
                size="md"
                radius="xl"
                type="submit"
                rightIcon={
                  <Windmill
                    size={24}
                    className={isLoading ? "animate-spin" : ""}
                  />
                }
              >
                Generate Images
              </Button>
              <Tooltip label="Reset form" position="bottom">
                <Button
                  variant="outline"
                  color="pink"
                  size="md"
                  radius="xl"
                  type="reset"
                  onClick={() => form.reset()}
                >
                  <RefreshDot size={24} />
                </Button>
              </Tooltip>
            </div>
          </div>
        </div>
        <div className="col-span-2 bg-white rounded-lg p-6 md:p-12">
          {STEP_ARRAY.map((step) => (
            <div
              key={step.key}
              className="border-b pb-6 mb-6 last-of-type:border-0"
            >
              <step.component />
            </div>
          ))}
        </div>
      </form>
    </FormProvider>
  );
}
