import type { ActionArgs } from "@remix-run/node";
import { json } from "@remix-run/node";
import { SLACK_TOKEN } from "~/lib/env";
import { resolveCdn } from "~/lib/resolveUrl";
import { sendNotification } from "~/lib/slack.server";
import { getSupabaseAdmin } from "~/lib/supabase";
import type { PromptResult } from "~/modules/prompts";

export async function action({ request }: ActionArgs) {
  const data = await request.json();

  const { promptId } = data;

  if (!promptId) {
    return json({
      error: "Prompt ID is required",
    });
  }

  const client = getSupabaseAdmin();

  const { data: prompt, error } = await client
    .from("prompts")
    .select("*, results:prompt_results(*)")
    .match({ id: promptId })
    .single();

  if (error) {
    return json(
      {
        error: error.message,
      },
      { status: 500 }
    );
  }

  if (!prompt) {
    return json(
      {
        error: "No prompt found",
      },
      { status: 500 }
    );
  }

  try {
    const url = new URL(request.url);
    const origin = url.origin;

    await sendNotification({
      blocks: [
        {
          type: "section",
          text: {
            type: "mrkdwn",
            text: prompt.prompt,
          },
        },
        ...prompt.results.map((result: PromptResult) => ({
          type: "image",
          image_url: resolveCdn(result.thumbfile!),
          alt_text: `result id: ${result.id}`,
        })),
        {
          type: "actions",
          elements: prompt.results.map(
            (result: PromptResult, index: number) => ({
              type: "button",
              text: {
                type: "plain_text",
                text: `Approve #${index + 1}`,
                emoji: true,
              },
              value: `${result.id}`,
              action_id: `result-${result.id}`,
              url: `${origin}/api/webhooks/slack?resultId=${result.id}&status=approved&key=${SLACK_TOKEN}`,
            })
          ),
        },
      ],
    });

    return json({
      success: true,
    });
  } catch (error: any) {
    return json(
      {
        error: error.message,
      },
      { status: 500 }
    );
  }
}
