import { useSearchParams } from "@remix-run/react";
import { classNames } from "~/lib/classNames";
import { resolveCdn } from "~/lib/resolveUrl";
import { useDesigner } from "./useDesigner";

export default function ImageSelector() {
  const { prompt } = useDesigner();
  const [searchParams, setSearchParams] = useSearchParams();

  if (!prompt) return null;
  const selectedResult = searchParams.get("resultId");

  return (
    <div className="w-20 lg:w-auto flex-none flex flex-col justify-center pl-2 lg:pl-0 lg:justify-start lg:flex-row lg:py-6 lg:-mx-2">
      {prompt.results.map((result) => {
        return (
          <div key={result.id} className="lg:px-2 w-full lg:w-1/4">
            <button
              type="button"
              className={classNames(
                "relative h-16 lg:h-36 w-full flex-none rounded-lg overflow-hidden shadow-lg",
                selectedResult && Number(selectedResult) === result.id
                  ? "ring-2 ring-offset-2 ring-violet-600"
                  : ""
              )}
              onClick={() => {
                setSearchParams({
                  ...Object.fromEntries(searchParams),
                  printImage: "designfile",
                  resultId: result.id.toString(),
                });
              }}
            >
              <img
                src={resolveCdn(result.filename)}
                alt={prompt.prompt}
                className="h-full w-full object-cover rounded-lg shadow-lg"
              />
            </button>
          </div>
        );
      })}
    </div>
  );
}
