import { atom } from "jotai";
import type { PromptRecord } from "~/modules/prompts";
import { ANON_PROMPT_KEY } from "../const";
import { atomWithLocalStorage } from "./withLocalStorage";

export const promptScope = Symbol("__prompt");
export const promptsAtom = atom<PromptRecord[]>([]);
export const promptAtom = atom<PromptRecord>({} as unknown as PromptRecord);

export const resetPromptAtom = atom(null, (_get, set) =>
  set(promptAtom, {} as unknown as PromptRecord)
);

/**
 * NEW STUFF
 */
export const promptHistoryDrawerAtom = atom<boolean>(false);
export const promptHistoryAtom = atomWithLocalStorage<PromptRecord[]>(
  ANON_PROMPT_KEY,
  []
);
export const pushPromptAtom = atom(null, (_get, set, prompt: PromptRecord) => {
  set(promptHistoryAtom, (prevHistory) => {
    const newPrompts: PromptRecord[] = [...prevHistory];
    const promptIndex = prevHistory.findIndex((p) => p.id === prompt.id);
    if (promptIndex > -1) {
      // exist update it
      newPrompts[promptIndex] = prompt;
    } else {
      newPrompts.push(prompt);
    }
    return newPrompts;
  });
});
