import { ActionIcon, Button, TextInput, Tooltip } from "@mantine/core";
import { useClipboard } from "@mantine/hooks";
import { showNotification } from "@mantine/notifications";
import { useEffect, useState } from "react";
import {
  BrandFacebook,
  BrandLinkedin,
  BrandPinterest,
  BrandReddit,
  BrandTwitter,
  Copy,
  Mail,
  Share,
} from "tabler-icons-react";

import {
  EmailShareButton,
  FacebookShareButton,
  LinkedinShareButton,
  PinterestShareButton,
  RedditShareButton,
  TwitterShareButton,
} from "react-share";

import { event, GTAG_EVENTS } from "~/lib/gtag.client";
import type { PromptRecord } from "~/modules/prompts";
import { resolveCdn } from "~/lib/resolveUrl";
import Modal from "./primitives/Modal";

export default function PublicShare({ prompt }: { prompt: PromptRecord }) {
  const { copy, copied } = useClipboard();
  const [url, setUrl] = useState<string>("");
  const [opened, setOpened] = useState<boolean>(false);

  useEffect(() => {
    if (copied) {
      showNotification({
        title: "Success",
        message: "Copied to your clipboard!",
        color: "green",
      });
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [copied]);

  useEffect(() => {
    setUrl(window.location.href);
  }, []);

  return (
    <>
      <Button
        radius="xl"
        variant="filled"
        color="pink"
        size="lg"
        onClick={() => setOpened(true)}
        leftIcon={<Share />}
      >
        Share
      </Button>
      <Modal
        size="sm"
        open={opened}
        onOpenChange={() => setOpened(false)}
        title="Share"
      >
        <div className="flex flex-col space-y-4">
          <TextInput
            mb={0}
            label="Copy Link"
            readOnly
            value={url}
            onChange={() => {}}
            sx={{ flex: 1 }}
            rightSection={
              <Tooltip label="Copy" position="left">
                <ActionIcon
                  color="dark"
                  variant="light"
                  onClick={() => {
                    event({
                      ...GTAG_EVENTS.PUBLIC_SHARE_COPY,
                      label: "public",
                    });
                    copy(url);
                  }}
                >
                  <Copy size={16} />
                </ActionIcon>
              </Tooltip>
            }
          />
          <div className="flex items-center justify-center space-x-4">
            <EmailShareButton
              url={url}
              className="h-10 w-10 rounded-full shadow-md flex items-center justify-center transition-all duration-150 transform hover:scale-110 hover:shadow-xl active:scale-95"
              style={{
                background: "#1e293b",
                color: "#ffffff",
              }}
              onClick={() => {
                event({
                  ...GTAG_EVENTS.PUBLIC_SHARE_COPY,
                  label: "email",
                });
              }}
              subject={`Prompt: ${prompt.prompt}`}
            >
              <Mail size={24} />
            </EmailShareButton>
            <FacebookShareButton
              url={url}
              className="h-10 w-10 rounded-full shadow-md flex items-center justify-center transition-all duration-150 transform hover:scale-110 hover:shadow-xl active:scale-95"
              style={{
                background: "#3b5998",
                color: "#ffffff",
              }}
              onClick={() => {
                event({
                  ...GTAG_EVENTS.PUBLIC_SHARE_COPY,
                  label: "facebook",
                });
              }}
              quote={`Prompt: ${prompt.prompt}`}
            >
              <BrandFacebook size={24} />
            </FacebookShareButton>
            <LinkedinShareButton
              url={url}
              className="h-10 w-10 rounded-full shadow-md flex items-center justify-center transition-all duration-150 transform hover:scale-110 hover:shadow-xl active:scale-95"
              style={{
                background: "#057eb1",
                color: "#ffffff",
              }}
              onClick={() => {
                event({
                  ...GTAG_EVENTS.PUBLIC_SHARE_COPY,
                  label: "linkedin",
                });
              }}
              title={`Prompt: ${prompt.prompt}`}
              source="Designed AI"
            >
              <BrandLinkedin size={24} />
            </LinkedinShareButton>
            <PinterestShareButton
              url={url}
              media={resolveCdn(prompt.results[0].filename)}
              className="h-10 w-10 rounded-full shadow-md flex items-center justify-center transition-all duration-150 transform hover:scale-110 hover:shadow-xl active:scale-95"
              style={{
                background: "#cb2128",
                color: "#ffffff",
              }}
              description={`Prompt: ${prompt.prompt}`}
              onClick={() => {
                event({
                  ...GTAG_EVENTS.PUBLIC_SHARE_COPY,
                  label: "pinterest",
                });
              }}
            >
              <BrandPinterest size={24} />
            </PinterestShareButton>
            <RedditShareButton
              url={url}
              className="h-10 w-10 rounded-full shadow-md flex items-center justify-center transition-all duration-150 transform hover:scale-110 hover:shadow-xl active:scale-95"
              style={{
                background: "#5f9acf",
                color: "#ffffff",
              }}
              title={`Prompt: ${prompt.prompt}`}
              onClick={() => {
                event({
                  ...GTAG_EVENTS.PUBLIC_SHARE_COPY,
                  label: "reddit",
                });
              }}
            >
              <BrandReddit size={24} />
            </RedditShareButton>
            <TwitterShareButton
              url={url}
              className="h-10 w-10 rounded-full shadow-md flex items-center justify-center transition-all duration-150 transform hover:scale-110 hover:shadow-xl active:scale-95"
              style={{
                background: "#08abed",
                color: "#ffffff",
              }}
              title={`Prompt: ${prompt.prompt}`}
              onClick={() => {
                event({
                  ...GTAG_EVENTS.PUBLIC_SHARE_COPY,
                  label: "twitter",
                });
              }}
            >
              <BrandTwitter size={24} />
            </TwitterShareButton>
          </div>
        </div>
      </Modal>
    </>
  );
}
