import { Button } from "@mantine/core";
import type { LoaderArgs } from "@remix-run/node";
import { json, redirect } from "@remix-run/node";
import { Form, Link, useLoaderData } from "@remix-run/react";
import { useAtomValue } from "jotai";
import { Quote } from "tabler-icons-react";
import { promptHistoryAtom } from "~/lib/atoms/prompts";
import { useUserData } from "~/lib/hooks/useUserData";
import { resolveCdn } from "~/lib/resolveUrl";
import { getSession, sessionStorage } from "~/lib/session.server";
import { getSupabaseAdmin } from "~/lib/supabase";
import { PRODUCT_VARIANTS } from "~/modules/printful";

export async function loader({ request }: LoaderArgs) {
  const session = await getSession(request);
  const url = new URL(request.url);
  if (!url.searchParams.has("hash")) {
    return redirect("/", {
      headers: {
        "Set-Cookie": await sessionStorage.destroySession(session),
      },
    });
  }
  const hash = Buffer.from(url.searchParams.get("hash")!, "base64").toString(
    "utf-8"
  );

  const client = getSupabaseAdmin();
  const { variantId, imageId, printImage } = JSON.parse(hash);

  const { data: result } = await client
    .from("prompt_results")
    .select("*, prompt:prompts(*)")
    .match({ id: Number(imageId) })
    .single();

  const productId = Object.keys(PRODUCT_VARIANTS).find((k) => {
    const variants =
      PRODUCT_VARIANTS[Number(k) as keyof typeof PRODUCT_VARIANTS];
    return variants.includes(variantId);
  });

  return json({
    result,
    productId,
    printImage,
  });
}

export default function ConfirmationPage() {
  const { result } = useLoaderData();
  const { user } = useUserData();
  const prompts = useAtomValue(promptHistoryAtom);

  return (
    <>
      <section id="hero" className="bg-gray-200">
        <div className="container max-w-screen-lg flex items-center justify-center py-48 -mt-20">
          <h1 className="text-6xl md:text-8xl font-black tracking-tighter text-slate-800">
            Thank you 🥳
          </h1>
        </div>
      </section>
      <section id="content" className="-mt-20 pb-20">
        <div className="container max-w-screen-lg bg-white p-6 md:p-12 rounded-lg grid grid-cols-2 gap-8 shadow-lg">
          <img
            src={resolveCdn(result.filename)}
            alt={result.prompt.prompt}
            className="rounded-lg shadow-xl"
          />
          <div className="relative self-center">
            <h2 className="text-3xl md:text-4xl font-black tracking-tighter text-slate-800 mb-6">
              {result.prompt.prompt}
              <Quote
                size={256}
                className="absolute top-0 right-0 transform translate-x-1/2 -translate-y-1/2 text-gray-900/25"
              />
            </h2>
            {!user ? (
              <Form method="post" action="/signup">
                {prompts.map((prompt, index) => (
                  <input
                    key={prompt.id}
                    type="hidden"
                    name={`promptIds`}
                    value={prompt.id}
                  />
                ))}
                <Button
                  variant="gradient"
                  gradient={{ from: "violet", to: "pink" }}
                  size="lg"
                  radius="xl"
                  type="submit"
                >
                  Create Account
                </Button>
              </Form>
            ) : (
              <Button
                component={Link}
                to="/app"
                variant="gradient"
                gradient={{ from: "violet", to: "pink" }}
                size="lg"
                radius="xl"
                type="submit"
              >
                My Account
              </Button>
            )}
          </div>
        </div>
      </section>
    </>
  );
}
