import { useMatches } from "@remix-run/react";
import type { ProductResponse, ProductVariant } from "~/modules/printful";
import type { PromptRecord, PromptResult } from "~/modules/prompts";

export function useDesigner() {
  const match = useMatches().find(
    (match) => match.handle?.context === "designer"
  );

  if (!match)
    throw new Error(
      "Must be inside designer context to use `useDesigner` hook"
    );

  return match.data as {
    product: ProductResponse;
    variant: ProductVariant;
    prompt: PromptRecord | null;
    result: PromptResult | null;
    printShape: "rectangle" | "square";
    printImage: "nobgfile" | "designfile";
  };
}
