import type { ActionArgs } from "@remix-run/node";
import { json } from "@remix-run/node";
import { requireAuthSession } from "~/lib/auth/guards.server";
import { deleteFile } from "~/lib/spaces";
import { getSupabaseAdmin } from "~/lib/supabase";

export async function action({ request, params }: ActionArgs) {
  const { promptId } = params as { promptId: string };
  const { accessToken } = await requireAuthSession(request);

  const client = getSupabaseAdmin(accessToken);

  const { data, error } = await client
    .from("prompts")
    .select("*, results:prompt_results(*)")
    .match({ uuid: promptId })
    .single();

  if (error) {
    throw new Response("", { status: 500, statusText: "Something went wrong" });
  }

  if (!data) {
    throw new Response("", { status: 404, statusText: "Prompt not found" });
  }

  // get results
  if (data.results) {
    const deletePromises = [];
    for (const result of data.results) {
      if (result.filename) {
        deletePromises.push(deleteFile(result.filename));
      }
      if (result.designfile) {
        deletePromises.push(deleteFile(result.designfile));
      }
      if (result.nobgfile) {
        deletePromises.push(deleteFile(result.nobgfile));
      }
      if (result.thumbfile) {
        deletePromises.push(deleteFile(result.thumbfile));
      }
    }
    if (deletePromises.length) {
      await Promise.all(deletePromises);
    }
  }

  // delete shit
  const { error: deleteError } = await client
    .from("prompts")
    .delete()
    .match({ uuid: promptId });

  if (deleteError) {
    throw new Response("", { status: 500, statusText: "Something went wrong" });
  }

  return json({
    success: true,
  });
}
