import { Anchor, Text, Textarea, Button } from "@mantine/core";
import { useForm, zodResolver } from "@mantine/form";
import { showNotification } from "@mantine/notifications";
import { useSetAtom } from "jotai";
import { z } from "zod";

import Progress from "~/components/Progress";
import { classNames } from "~/lib/classNames";
import { pushPromptAtom } from "~/lib/atoms/prompts";
import { event, GTAG_EVENTS } from "~/lib/gtag.client";
import { useGenerateImage } from "~/lib/hooks/useGenerateImage";
import { randomizePrompt } from "~/lib/randomizePrompt";
import { useCreatePrompt } from "~/modules/prompts";
import type { PromptRecord } from "~/modules/prompts";
import { useSearchParams } from "@remix-run/react";
import { useDesigner } from "./useDesigner";

const PromptSchema = z.object({
  prompt: z.string().transform((val, ctx) => {
    if (!val || val.split(" ").length < 3) {
      ctx.addIssue({
        code: z.ZodIssueCode.custom,
        message: "Prompt is required and must be greater than 3 words",
      });
      return z.NEVER;
    }
    return val;
  }),
});

export default function PromptForm() {
  const { prompt } = useDesigner();
  const setPrompts = useSetAtom(pushPromptAtom);
  const [searchParams, setSearchParams] = useSearchParams();

  const { values, ...form } = useForm({
    validate: zodResolver(PromptSchema),
    initialValues: {
      prompt: prompt?.prompt ?? "",
    },
  });

  const generateImage = useGenerateImage();
  const createPrompt = useCreatePrompt({
    onSuccess: (data: PromptRecord) => {
      generateImage.mutate(
        {
          promptId: data.id,
          imageCount: 4,
        },
        {
          onSuccess: (genImageData) => {
            const newPrompt = {
              ...data,
              results: genImageData.results,
            };

            setPrompts(newPrompt);
            setSearchParams({
              ...Object.fromEntries(searchParams),
              promptId: data.uuid,
              resultId: genImageData.results[0].id.toString(),
            });
          },
          onError(error, variables, context) {
            showNotification({
              title: "Whoops",
              message: (
                <Text>
                  Something went wrong. Please review your prompt and try again.
                  Ensure your prompt follows the DALL-E{" "}
                  <Anchor
                    href="https://labs.openai.com/policies/content-policy"
                    target="_blank"
                    rel="noopener"
                  >
                    content policy
                  </Anchor>
                  {""}.
                </Text>
              ),
              color: "red",
            });
          },
        }
      );
    },
  });

  const randomize = () => {
    event({
      ...GTAG_EVENTS.SURPRISE_ME,
      label: "home",
    });

    const prompt = randomizePrompt();

    form.setFieldValue("prompt", prompt);
  };

  const onSubmit = (formValues: typeof values) => {
    event({
      ...GTAG_EVENTS.CREATE_PROMPT,
      label: "home",
    });
    createPrompt.mutate({
      prompt: formValues.prompt,
    });
  };

  const isLoading = generateImage.isLoading || createPrompt.isLoading;

  return (
    <form
      onSubmit={form.onSubmit(onSubmit)}
      className="bg-white p-6 rounded-lg shadow-xl"
    >
      <Textarea
        label={
          <span className="flex items-center mb-2">
            <p className="text-base">Start with a detailed description, or </p>
            <Button
              compact
              size="sm"
              ml={6}
              variant="gradient"
              gradient={{ from: "violet", to: "pink" }}
              onClick={() => randomize()}
            >
              Surprise Me
            </Button>
          </span>
        }
        variant="filled"
        size="xl"
        placeholder="icon of a F1 car, synthwave vector art, gradients, vibrant color, 8K"
        radius="md"
        minRows={2}
        autosize
        {...form.getInputProps("prompt")}
      ></Textarea>
      <div className="flex flex-col lg:flex-row lg:items-center mt-4">
        <Button
          // variant="gradient"
          // gradient={{ from: "violet", to: "pink" }}
          variant="outline"
          color="pink"
          size="md"
          radius="xl"
          type="submit"
          mr={16}
          loading={isLoading}
          loaderPosition="right"
        >
          Generate Image
        </Button>
        <div
          className={classNames(
            "text-slate-600 ml-4 mt-6 lg:mt-0",
            isLoading ? "block flex-1" : "hidden"
          )}
        >
          <Progress loading={isLoading} />
          <p className="mt-1 text-sm">
            Generating images may take up to one minute.
          </p>
        </div>
      </div>
    </form>
  );
}
