import type { PostgrestError } from "@supabase/supabase-js";
import type { MutationOptions } from "@tanstack/react-query";
import { useMutation } from "@tanstack/react-query";
import { useAtomValue } from "jotai";
import { accessTokenAtom, userAtom } from "~/lib/atoms/root";
import { getSupabase } from "~/lib/supabase";
import type { QueryBase } from "~/lib/types";
import { SELECT, TABLE_NAME } from "./const";
import type { NewPromptRecord, PromptRecord } from "./schema";

export async function createPrompt({
  values,
  accessToken,
}: QueryBase<NewPromptRecord>) {
  const client = getSupabase(accessToken);
  const { data, error } = await client
    .from(TABLE_NAME)
    .insert(values)
    .select(SELECT)
    .single();

  if (error) throw error;

  return data as PromptRecord;
}

export function useCreatePrompt(
  options: MutationOptions<
    PromptRecord,
    PostgrestError,
    Omit<NewPromptRecord, "created_by">
  > = {}
) {
  const accessToken = useAtomValue(accessTokenAtom);
  const user = useAtomValue(userAtom);

  return useMutation(
    (values: Omit<NewPromptRecord, "created_by">) =>
      createPrompt({
        values: { ...values, created_by: user?.id },
        accessToken,
      }),
    {
      ...options,
    }
  );
}
