import { Button, TextInput } from "@mantine/core";
import { useForm } from "@mantine/form";
import type { ActionArgs, LoaderArgs } from "@remix-run/node";
import { json } from "@remix-run/node";
import {
  Form,
  useLoaderData,
  useSubmit,
  useTransition,
} from "@remix-run/react";
import { useRef } from "react";
import { requireAuthSession } from "~/lib/auth/guards.server";
import { getSupabaseAdmin } from "~/lib/supabase";
import type { ProfileRecord } from "~/modules/profiles";

export async function loader({ request }: LoaderArgs) {
  const { userId, accessToken } = await requireAuthSession(request);
  const client = getSupabaseAdmin(accessToken);
  const { data } = await client
    .from("profiles")
    .select("*")
    .match({ id: userId })
    .single();

  return json({
    profile: data,
  });
}

export async function action({ request }: ActionArgs) {
  const { userId, email, accessToken } = await requireAuthSession(request);
  const formData = Object.fromEntries(await request.formData());

  const client = getSupabaseAdmin(accessToken);

  await client
    .from("profiles")
    .update({
      full_name: formData.full_name,
      email: formData.email,
    })
    .match({ id: userId });

  if (email !== formData.email) {
    await client.auth.updateUser({
      email: formData.email as string,
    });
  }

  return json({
    success: true,
  });
}

export default function AccountPage() {
  const { profile } = useLoaderData<{ profile: ProfileRecord }>();
  const submit = useSubmit();
  const formRef = useRef<HTMLFormElement>(null);
  const transition = useTransition();

  const form = useForm({
    initialValues: {
      full_name: profile.full_name,
      email: profile.email,
    },
  });

  const onSubmit = (values: typeof form.values) => {
    submit(formRef.current, {
      method: "post",
    });
  };

  return (
    <>
      <section id="hero" className="bg-gray-200">
        <div className="container max-w-screen-md flex items-center justify-center py-32 -mt-20">
          <h1 className="text-6xl md:text-8xl font-black tracking-tighter max-w-screen-lg text-slate-800 text-center">
            Your Account
          </h1>
        </div>
      </section>
      <section id="content" className="-mt-20 pb-20">
        <div className="container max-w-screen-md bg-white p-6 md:p-12 rounded-lg">
          <Form
            ref={formRef}
            method="post"
            onSubmit={form.onSubmit(onSubmit)}
            className="space-y-4"
          >
            <TextInput
              name="full_name"
              label="Full Name"
              {...form.getInputProps("full_name")}
            />
            <TextInput
              name="email"
              label="Email"
              description="Changing your email will require you to verify your email again."
              {...form.getInputProps("email")}
            />
            <Button
              variant="gradient"
              gradient={{ from: "violet", to: "pink" }}
              size="md"
              radius="xl"
              type="submit"
              loading={transition.state === "submitting"}
            >
              Save
            </Button>
          </Form>
        </div>
      </section>
    </>
  );
}
