import { Link } from "@remix-run/react";
import { Clock } from "tabler-icons-react";
import { useGetPrompt } from "~/modules/prompts";
import type { PromptRecord } from "~/modules/prompts";
import { resolveThumbnail } from "~/lib/resolveUrl";
import { timeAgo } from "~/lib/timeAgo";
import { Button } from "@mantine/core";
// import { useLikeable } from "~/lib/hooks/useLikeable";

export interface PromptCardProps {
  prompt: PromptRecord;
  asCarousel?: boolean;
}

export default function PromptCard({
  prompt: initialPrompt,
  asCarousel = false,
}: PromptCardProps) {
  const { data: prompt } = useGetPrompt(initialPrompt.uuid, {
    initialData: initialPrompt,
  });
  // const toggleLiked = useLikeable(prompt);

  if (!prompt) return null;

  const result = prompt.results.find((r) => r.featured) ?? prompt.results[0];

  return (
    <div
      className="bg-white relative rounded-lg shadow-lg p-2 md:p-4 will-change-transform transition-all transform hover:-translate-y-1 hover:shadow-xl"
      key={prompt.id}
    >
      <Link
        to={`/explore/${prompt.uuid}`}
        className="absolute inset-0 z-0"
        aria-labelledby={prompt.prompt}
      >
        <span className="sr-only">Prompt #{prompt.id}</span>
      </Link>
      <div className="overflow-hidden rounded-lg aspect-1 bg-black/50 backdrop-blur-md relative mb-2 md:mb-4">
        <Link to={`/explore/${prompt.uuid}`}>
          <img
            src={resolveThumbnail(result)}
            alt={prompt.prompt}
            width="256"
            height="256"
            loading="lazy"
            className="h-full w-full object-cover"
          />
        </Link>
      </div>
      <div className="relative px-2 md:px-4 flex justify-between items-center pointer-events-none">
        {/* <button
          onClick={() => toggleLiked()}
          className="pointer-events-auto absolute right-2 md:right-4 top-0 transform -translate-y-1/2 rounded-full h-12 md:h-16 w-12 md:w-16 bg-gray-200 p-1 md:p-2 border-2 md:border-4 border-white transition-all duration-150 hover:p-0 active:scale-95"
        >
          <div className="bg-white shadow-2xl h-full w-full rounded-full flex flex-col items-center justify-center">
            <Heart
              size={16}
              className={classNames(
                "text-pink-600",
                isLiked ? "fill-pink-600" : "",
                isLiked ? "animate-[ping_750ms_ease-in-out]" : ""
              )}
            />
            <p className="text-[8px]">{likes?.likes ?? 0}</p>
          </div>
        </button> */}

        <div>
          <h2 className="block text-lg leading-none">Prompt #{prompt.id}</h2>
          <p className="text-xs text-gray-600 flex items-center">
            <Clock size={14} className="mr-1" />
            {timeAgo(prompt.created_at)}
          </p>
        </div>
        <Button
          className="pointer-events-auto"
          component={Link}
          to={`/designer?promptId=${prompt.uuid}&resultId=${result.id}`}
          radius="xl"
          variant="gradient"
          gradient={{ from: "violet", to: "pink" }}
        >
          Shop
        </Button>
      </div>
    </div>
  );
}
