import type { LoaderArgs } from "@remix-run/node";
import { redirect } from "@remix-run/node";
import sharp from "sharp";
import { resolveCdn, resolveUrl } from "~/lib/resolveUrl";
import { uploadFile } from "~/lib/spaces";
import { getSupabaseAdmin } from "~/lib/supabase";

// api/transforms/thumbnail/45.png

async function placeholder() {
  return await sharp({
    create: {
      width: 1024,
      height: 1024,
      channels: 4,
      background: { r: 255, g: 255, b: 255, alpha: 1 },
    },
  })
    .png()
    .toBuffer();
}

async function getRemoteFile(url: string) {
  const imageResponse = await fetch(url);
  const imageBuffer = await imageResponse.arrayBuffer();
  return imageBuffer;
}

export async function loader({ request, params }: LoaderArgs) {
  const { id } = params as { id: string };

  const client = getSupabaseAdmin();
  const { data, error } = await client
    .from("prompt_results")
    .select("id, filename, thumbfile")
    .match({ id })
    .single();

  if (!data || error) {
    const ph = await placeholder();
    return new Response(ph);
  }

  if (data.thumbfile) {
    return redirect(resolveCdn(data.thumbfile));
  }

  // doesnt exist
  const originalFile = await getRemoteFile(resolveUrl(data.filename));
  const buffer = Buffer.from(originalFile);

  const thumbPath = data.filename
    .replace("img-", "thumb-")
    .replace(".png", ".webp");
  const thumbNail = await sharp(buffer)
    .resize()
    .webp({ quality: 70 })
    .toBuffer();
  await uploadFile(thumbPath, thumbNail);

  await client
    .from("prompt_results")
    .update({
      thumbfile: thumbPath,
    })
    .match({ id })
    .single();

  return redirect(resolveCdn(thumbPath));
}
