import type { PostgrestError } from "@supabase/supabase-js";
import type { MutationOptions } from "@tanstack/react-query";
import { useMutation } from "@tanstack/react-query";
import { useAtomValue, useSetAtom } from "jotai";
import { promptAtom, promptScope } from "~/lib/atoms/prompts";
import { accessTokenAtom } from "~/lib/atoms/root";
import { getSupabase } from "~/lib/supabase";
import type { QueryBase } from "~/lib/types";
import { SELECT, TABLE_NAME } from "./const";
import type { PromptRecord } from "./schema";
import { PromptSchema } from "./schema";

type UpdatePromptRecord = Partial<PromptRecord> & { id: number };

export async function updatePrompt({
  values,
  accessToken,
}: QueryBase<UpdatePromptRecord>) {
  const client = getSupabase(accessToken);

  const validation = PromptSchema.omit({ results: true }).safeParse(values);
  if (!validation.success) {
    throw validation.error;
  }

  const { id, prompt, selections, created_by } = validation.data;

  const { data, error } = await client
    .from(TABLE_NAME)
    .upsert({ id, prompt, selections, created_by })
    .select(SELECT)
    .single();

  if (error) throw error;

  return data as PromptRecord;
}

export function useUpdatePrompt(
  options: MutationOptions<
    PromptRecord,
    PostgrestError,
    UpdatePromptRecord
  > = {}
) {
  const accessToken = useAtomValue(accessTokenAtom);
  const setPrompt = useSetAtom(promptAtom, promptScope);

  return useMutation(
    (values: UpdatePromptRecord) => updatePrompt({ values, accessToken }),
    {
      ...options,
      onSuccess: (data, ...args) => {
        setPrompt(data);
        if (options.onSuccess) {
          options.onSuccess(data, ...args);
        }
      },
    }
  );
}
