// ---------------------------------------------
/**
 * PromptDetail Page should be the ecommerce product detail
 * page.
 *
 * From here, you can choose the type of gear you want with that design
 *
 */
// ---------------------------------------------
import type { LoaderArgs, MetaFunction } from "@remix-run/node";
import { json } from "@remix-run/node";
import { Outlet, useLoaderData, useParams } from "@remix-run/react";

import { resolveCdn, resolveUrl } from "~/lib/resolveUrl";
import { getSupabaseAdmin } from "~/lib/supabase";
import { useGetPrompt } from "~/modules/prompts";
import type { PromptRecord } from "~/modules/prompts";
import { getAuthSession } from "~/lib/session.server";
import FastColor from "~/components/FastColor";
import PromptHero from "~/components/PromptHero";

export const meta: MetaFunction = ({ location, data }) => {
  const prompt = data?.prompt?.prompt;
  const ogImage = data?.prompt?.results?.[0].filename;
  const title = prompt
    ? `${prompt.slice(0, 30)}...`
    : "Prompt, Design and Shop";

  return {
    charset: "utf-8",
    title: `${title}`,
    description: prompt ?? undefined,
    "og:description": prompt ?? undefined,
    viewport: "width=device-width,initial-scale=1",
    "twitter:card": "summary_large_image",
    "twitter:site": "@francoxavier33",
    "twitter:creator": "@francoxavier33",
    "twitter:image": resolveCdn(ogImage ?? "static/social/open-graph.jpg"),
    "og:image": resolveCdn(ogImage ?? "static/social/open-graph.jpg"),
    "og:type": "website",
    "og:url": `https://www.designedwithai.com${location.pathname}`,
  };
};

export async function loader({ request, params }: LoaderArgs) {
  const authSession = await getAuthSession(request);
  const { promptId } = params as { promptId: string };
  const client = getSupabaseAdmin();

  const query = client
    .from("prompts")
    .select(
      "*, results:prompt_results!inner(*), like_count:likes(likes), is_liked:prompt_likes(user_id)"
    )
    .match({ uuid: promptId });

  if (authSession?.userId) {
    query.eq("is_liked.user_id", authSession.userId);
  }

  const { data, error } = await query.single();

  if (error) {
    throw new Response("", { status: 500, statusText: error.message });
  }

  if (!data) {
    throw new Response("", { status: 404, statusText: "Prompt not found" });
  }

  return json({
    prompt: data,
  });
}

export function action() {
  return { ok: true };
}

export const handle = {
  outlet: "prompt-detail",
};

export default function PromptDetailPage() {
  const { prompt: initialPrompt } = useLoaderData<{ prompt: PromptRecord }>();
  const params = useParams();
  const { data: prompt } = useGetPrompt(initialPrompt.uuid, {
    initialData: initialPrompt,
  });

  if (!prompt) return null;

  const showHero = !params.imageId;

  return (
    <>
      <section id="hero" className="bg-gray-200 relative">
        <FastColor
          className="absolute inset-0"
          image={resolveUrl(prompt.results![0].filename)}
        ></FastColor>
        <div className="container flex flex-col md:flex-row md:items-center py-20 md:py-24 -mt-20">
          {showHero && <PromptHero prompt={prompt} />}
        </div>
      </section>
      <Outlet context={{ prompt: prompt! }} />
    </>
  );
}
