import { Progress as MTProgress } from "@mantine/core";
import { useInterval } from "@mantine/hooks";
import { useEffect, useMemo, useState } from "react";

const MAX_TICKER = 100 * 10 * 60;

export default function Progress({ loading }: { loading: boolean }) {
  const [ticker, setTicker] = useState(0);
  const { start, stop } = useInterval(() => {
    setTicker((prev) => (prev += 100));
  }, 100);

  useEffect(() => {
    if (loading) {
      start();
    } else {
      stop();
      setTicker(0);
    }
  }, [loading, start, stop]);

  const progress = useMemo(() => {
    return (ticker / MAX_TICKER) * 100;
  }, [ticker]);

  if (!loading) return null;

  return <MTProgress radius="xl" value={progress} color="pink" animate />;
}
