import type { PostgrestError } from "@supabase/supabase-js";
import type { MutationOptions } from "@tanstack/react-query";
import { useMutation } from "@tanstack/react-query";
import { useAtomValue } from "jotai";
import { accessTokenAtom } from "~/lib/atoms/root";
import type { QueryBase } from "~/lib/types";

type DeletePromptRecord = { uuid: string };

export async function deletePrompt({
  values,
  accessToken,
}: QueryBase<DeletePromptRecord>) {
  const { uuid } = values;
  const res = await fetch(`/api/delete/${uuid}`, {
    method: "post",
  });

  const data = await res.json();

  if (data.error) {
    throw data.error;
  }

  return data;
}

export function useDeletePrompt(
  options: MutationOptions<void, PostgrestError, DeletePromptRecord> = {}
) {
  const accessToken = useAtomValue(accessTokenAtom);

  return useMutation(
    (values: DeletePromptRecord) => deletePrompt({ values, accessToken }),
    {
      ...options,
    }
  );
}
