import type { LoaderArgs } from "@remix-run/node";
import { json } from "@remix-run/node";
import { useLoaderData } from "@remix-run/react";
import { useQueryClient } from "@tanstack/react-query";
import { useEffect } from "react";
import HomePageHero from "~/components/HomePageHero";
import OverflowGrid from "~/components/OverflowGrid";
import { getSupabaseAdmin } from "~/lib/supabase";
import type { PromptRecord } from "~/modules/prompts";
import CollectionPreview from "~/components/CollectionPreview";
import { collections } from "~/lib/collections";

export async function loader({ request }: LoaderArgs) {
  const client = getSupabaseAdmin();
  const query = client
    .from("featured_prompts")
    .select("*, results:prompt_results!inner(*)")
    .not("prompt", "eq", "")
    .order("created_at", { ascending: false })
    .limit(16);

  const { data, error } = await query;
  if (error) {
    return json({
      featured: [],
    });
  }

  return json({
    featured: data,
  });
}

export default function HomePage() {
  const queryClient = useQueryClient();
  const { featured } = useLoaderData<{
    featured: PromptRecord[];
  }>();

  useEffect(() => {
    featured.forEach((prompt) => {
      queryClient.setQueryData(["prompts", prompt.uuid], prompt);
    });
  }, [queryClient, featured]);

  return (
    <>
      <HomePageHero />

      {collections.map((collection) => {
        return <CollectionPreview key={collection.slug} {...collection} />;
      })}

      <OverflowGrid prompts={featured} />
    </>
  );
}
