import {
  PromptForm,
  ProductSelector,
  ProductPreview,
  ProductDetails,
  ImageSelector,
} from "~/components/designer";
import type { PromptResult } from "~/modules/prompts";
import type { PromptRecord } from "~/modules/prompts";
import type { LoaderArgs, MetaFunction } from "@remix-run/node";
import { json } from "@remix-run/node";
import { getSupabaseAdmin } from "~/lib/supabase";
import type { ProductResponse, ProductVariant } from "~/modules/printful";
import { AVAILABLE_PRODUCT_IDS, PRODUCT_VARIANTS } from "~/modules/printful";
import { getProduct } from "~/lib/printful.server";
import type { SyntheticEvent } from "react";
import { useLoaderData, useSubmit } from "@remix-run/react";
import { z } from "zod";
import { showNotification } from "@mantine/notifications";
import { Text } from "@mantine/core";
import { resolveCdn } from "~/lib/resolveUrl";

export const meta: MetaFunction = ({ data }) => {
  if (data?.result) {
    return {
      title: "Design Studio | Designed AI",
      "twitter:image": resolveCdn(data.result.filename),
      "og:image": resolveCdn(data.result.filename),
    };
  }

  return {
    title: "Design Studio | Designed AI",
  };
};

export async function loader({ request }: LoaderArgs) {
  const { searchParams } = new URL(request.url);

  const defaultProduct = AVAILABLE_PRODUCT_IDS[0];
  const defaultVariant = PRODUCT_VARIANTS[defaultProduct][0];

  const promptId = searchParams.get("promptId");
  const resultId = searchParams.get("resultId");
  const productId = searchParams.get("productId") ?? defaultProduct;
  const variantId = searchParams.get("variantId") ?? defaultVariant;
  const printShape = searchParams.get("printShape") ?? "rectangle";
  const printImage = searchParams.get("printImage") ?? "designfile";

  if (!AVAILABLE_PRODUCT_IDS.includes(Number(productId))) {
    throw new Response("", {
      status: 500,
      statusText: "Product not available",
    });
  }

  const product = await getProduct(Number(productId));
  const variant = product.variants.find((v) => v.id === Number(variantId))!;

  if (promptId) {
    const client = getSupabaseAdmin();
    const { data } = await client
      .from("prompts")
      .select("*, results:prompt_results(*)")
      .match({ uuid: promptId })
      .single();

    if (data) {
      return json({
        product,
        variant,
        prompt: data as PromptRecord,
        printShape,
        printImage,
        result: resultId
          ? data.results.find((r: PromptResult) => r.id === Number(resultId))
          : data.results[0],
      });
    }
  }

  return json({
    prompt: null,
    result: null,
    product,
    printShape,
    printImage,
    variant,
  });
}

export function action() {
  return { ok: true };
}

export const handle = {
  context: "designer",
};

const CheckoutFormSchema = z.object({
  productId: z.string(),
  variantId: z.string(),
  promptId: z.string(),
  imageId: z.string(),
});

export default function DesignerPage() {
  const { product, variant, prompt, result } = useLoaderData<{
    product: ProductResponse;
    variant: ProductVariant;
    prompt: PromptRecord | null;
    result: PromptResult | null;
    printShape: "rectangle" | "square";
    printImage: "nobgfile" | "designfile";
  }>();
  const submit = useSubmit();

  const onSubmit = (e: SyntheticEvent<HTMLFormElement>) => {
    e.preventDefault();
    console.log(e.target);
    const formData = new FormData(e.currentTarget);
    const validation = CheckoutFormSchema.safeParse(
      Object.fromEntries(formData)
    );

    if (!validation.success) {
      const missingPrompt = !!validation.error.errors.find((e) =>
        e.path.includes("promptId")
      );
      const missingImage = !!validation.error.errors.find((e) =>
        e.path.includes("imageId")
      );

      showNotification({
        title: "Whoops",
        message: (
          <>
            <Text>
              Order cannot be placed. Looks like there are missing items.
            </Text>
            {missingPrompt && <Text>You must first write a prompt.</Text>}
            {missingImage && <Text>You must select a generated image.</Text>}
          </>
        ),
      });
      return;
    }

    submit(formData, {
      method: "post",
      action: "/api/purchase/initiate",
    });
  };

  return (
    <>
      <form id="checkoutform" onSubmit={onSubmit}>
        <input type="hidden" name="productId" value={`${product.product.id}`} />
        <input type="hidden" name="variantId" value={`${variant.id}`} />
        {prompt && (
          <input type="hidden" name="promptId" value={`${prompt.id}`} />
        )}
        {result && (
          <input type="hidden" name="imageId" value={`${result.id}`} />
        )}
      </form>

      <section className="container grid grid-rows-8 lg:grid-cols-2 lg:grid-rows-3 gap-6 xl:gap-12 my-8">
        <div className="">
          <PromptForm />
        </div>
        <div className="row-span-4 lg:col-start-1 lg:row-start-2 lg:row-span-2">
          <ProductSelector />
          <ProductDetails />
        </div>
        <div className="row-start-2 row-span-3 lg:col-start-2 lg:row-start-1 lg:row-span-3 flex md:block">
          <ProductPreview />
          <ImageSelector />
        </div>
      </section>
    </>
  );
}
