import type { ActionArgs } from "@remix-run/node";
import { json } from "@remix-run/node";
import sharp from "sharp";
import { resolveUrl } from "~/lib/resolveUrl";
import { uploadFile } from "~/lib/spaces";
import { getSupabaseAdmin } from "~/lib/supabase";

export async function action({ request }: ActionArgs) {
  const data = await request.json();

  const { resultId } = data;

  if (!resultId) {
    return json({
      error: "Prompt ID is required",
    });
  }

  const client = getSupabaseAdmin();

  const { data: result, error } = await client
    .from("prompt_results")
    .select("*, prompt:prompts(*)")
    .match({ id: resultId })
    .single();

  if (error) {
    return json(
      {
        error: error.message,
      },
      { status: 500 }
    );
  }

  if (!result) {
    return json(
      {
        error: "No prompt results found",
      },
      { status: 500 }
    );
  }

  const { filename, prompt } = result;
  if (!filename || !filename.startsWith("http")) {
    return json({
      success: true,
    });
  }

  const fileName = new URL(resolveUrl(filename)).pathname
    .split("/")
    .pop() as string;

  const filePath = [
    "generated",
    `prompt-${prompt.id}`,
    `result-${result.id}`,
    fileName,
  ].join("/");

  const thumbPath = filePath.replace("img-", "thumb-").replace(".png", ".webp");

  try {
    const imageResponse = await fetch(resolveUrl(filename));
    const imageBuffer = await imageResponse.arrayBuffer();
    const thumbBuffer = await sharp(Buffer.from(imageBuffer))
      .resize()
      .webp({ quality: 70 })
      .toBuffer();

    await Promise.all([
      uploadFile(filePath, imageBuffer),
      uploadFile(thumbPath, thumbBuffer),
    ]);
  } catch (error) {
    // shh
    return json(
      {
        error: "Error transferring image",
      },
      { status: 500 }
    );
  }

  await client
    .from("prompt_results")
    .upsert({
      id: resultId,
      prompt_id: result.prompt_id,
      filename: filePath,
      thumbfile: thumbPath,
    })
    .select("*");

  return json({
    success: true,
  });
}
