import type { ActionArgs } from "@remix-run/node";
import { json } from "@remix-run/node";
import { RequiredError } from "openai/dist/base";
import { removeBg } from "~/lib/removebg.server";
import { resizeImage } from "~/lib/resize.server";
import { resolveUrl } from "~/lib/resolveUrl";
import { uploadFile } from "~/lib/spaces";
import { getSupabaseAdmin } from "~/lib/supabase";

export async function action({ request }: ActionArgs) {
  const data = await request.json();
  const { imageId } = data;

  if (!imageId) {
    return json({
      error: "Prompt ID is required",
    });
  }

  const client = getSupabaseAdmin();

  const { data: result, error } = await client
    .from("prompt_results")
    .select("*")
    .match({ id: imageId })
    .single();

  if (error) {
    return json(
      {
        error: error.message,
      },
      { status: 500 }
    );
  }

  if (!result) {
    return json(
      {
        error: "No result found",
      },
      { status: 500 }
    );
  }

  if (result.nobgfile) {
    return json({
      result,
    });
  }

  // let finalFile: string = result.designfile;
  // if (!result.designfile) {
  //   const imageBuffer = await resizeImage(resolveUrl(result.filename), {
  //     height: 4000,
  //     width: 4000,
  //   });
  //   const filename = result.filename.split("/").pop();
  //   const filePath = ["hi-res", `result-${result.id}`, filename].join("/");

  //   await uploadFile(filePath, imageBuffer);

  //   await client
  //     .from("prompt_results")
  //     .update({
  //       designfile: filePath,
  //     })
  //     .match({ id: result.id });

  //   finalFile = filePath;
  // }

  try {
    // const image = resolveUrl(finalFile);

    const imageBuffer = await removeBg(resolveUrl(result.filename));

    const filename = result.filename.split("/").pop();
    const hash = new Date().getTime();
    const filePath = [
      "hi-res",
      `result-${result.id}`,
      `nobg-${hash}-${filename}`,
    ].join("/");

    await uploadFile(filePath, imageBuffer);

    const { data: newResult } = await client
      .from("prompt_results")
      .update({
        nobgfile: filePath,
      })
      .match({ id: result.id })
      .select("*")
      .single();

    return json({
      result: newResult,
    });
  } catch (e) {
    console.log("error", e);
    if (e instanceof RequiredError) {
      return json(
        {
          error: e.message,
        },
        { status: 500 }
      );
    }
    return json(
      {
        error: (e as any)?.message ?? "Unknown error",
      },
      { status: 500 }
    );
  }
}

