import { useMutation } from "@tanstack/react-query";
import type { PromptResult } from "~/modules/prompts";

export function useGenerateImage() {
  return useMutation(
    async ({
      promptId,
      imageCount,
    }: {
      promptId: number;
      imageCount?: number;
    }): Promise<{ results: PromptResult[] }> => {
      const res = await fetch("/api/generate/image", {
        method: "post",
        headers: {
          "Content-type": "application/json",
        },
        body: JSON.stringify({ promptId, imageCount }),
      });
      const data = await res.json();
      if (data.error) {
        throw data.error;
      }

      // non blocking
      const promises = data.results.map((result: PromptResult) => {
        return fetch("/api/generate/upload", {
          method: "post",
          headers: {
            "Content-type": "application/json",
          },
          body: JSON.stringify({ resultId: result.id }),
        });
      });

      try {
        await Promise.all(promises);

        // side effect
        await fetch("/api/generate/notify", {
          method: "post",
          headers: {
            "Content-type": "application/json",
          },
          body: JSON.stringify({ promptId }),
        });
      } catch (error) {
        // retry ?
      }

      return data;
    }
  );
}
