import { useSearchParams } from "@remix-run/react";
import { useSetAtom } from "jotai";
import { printfulDataAtom } from "~/lib/atoms/printful";
import { classNames } from "~/lib/classNames";
import {
  AVAILABLE_PRODUCT_IDS,
  PRODUCT_VARIANTS,
  useGetProducts,
} from "~/modules/printful";
import { useDesigner } from "./useDesigner";

export default function ProductSelector() {
  const { product: selectedProduct } = useDesigner();
  const [searchParams, setSearchParams] = useSearchParams();
  const setPrintfulData = useSetAtom(printfulDataAtom);

  const { data: products, isLoading } = useGetProducts(AVAILABLE_PRODUCT_IDS, {
    onSuccess: (data) => {
      setPrintfulData((prev) => ({
        ...prev,
        products: data.map((d) => {
          const allowedVariants = PRODUCT_VARIANTS[d.product.id];
          return {
            ...d,
            variants: d.variants.filter((variant) =>
              typeof allowedVariants[0] === "string" &&
              allowedVariants[0] === "*"
                ? true
                : allowedVariants.includes(variant.id)
            ),
          };
        }),
      }));
    },
  });

  if (isLoading) {
    return (
      <div className="flex overflow-x-auto -mx-2 pb-4">
        {Array.from({ length: 4 }).map((_, index) => (
          <div key={index} className="aspect-[1/1] w-1/4 flex-none px-2">
            <div className="bg-gray-200 h-full w-full animate-pulse rounded-lg"></div>
          </div>
        ))}
      </div>
    );
  }

  return (
    <div className="flex overflow-x-auto -mx-2 pb-6">
      {products?.map((product) => (
        <div
          key={product.product.id}
          className="aspect-[1/1] w-1/4 flex-none p-2"
        >
          <button
            type="button"
            onClick={() => {
              setSearchParams({
                ...Object.fromEntries(searchParams),
                productId: product.product.id.toString(),
                variantId: product.variants
                  .find(
                    (v) => v.id === PRODUCT_VARIANTS[product.product.id][0]
                  )!
                  .id.toString(),
              });
            }}
            className={classNames(
              "bg-white group h-full w-full self-start text-left group relative flex flex-col items-start justify-start rounded-lg shadow-lg overflow-hidden transition-all duration-150 hover:shadow-xl",
              selectedProduct.product.id === product.product.id
                ? "ring-2 ring-offset-2 ring-violet-600"
                : ""
            )}
          >
            {product.product.image && (
              <img
                src={product.product.image}
                alt={product.product.title}
                className="object-cover h-full w-full"
              />
            )}
          </button>
        </div>
      ))}
    </div>
  );
}
