import { Avatar, CheckIcon, ColorSwatch, Group, Tooltip } from "@mantine/core";
import { colorTypes } from "~/lib/promptGenerator";
import { useFormContext } from "./FormProvider";

export default function StepColor() {
  const form = useFormContext();

  return (
    <>
      <div className="mb-4">
        <h2 className="font-black tracking-tighter text-2xl">Pick a Color</h2>
        <p className="text-gray-600">What color of the result do you want?</p>
      </div>
      <div className="flex flex-wrap mt-4">
        <Group>
          {colorTypes.map((option) => {
            return (
              <Tooltip
                key={option.value}
                label={option.label}
                position="bottom"
                styles={{ tooltip: { textTransform: "capitalize" } }}
              >
                {option.type === "color" ? (
                  <ColorSwatch
                    component="button"
                    type="button"
                    size={38}
                    color={option.hex ?? "white"}
                    onClick={() => form.setFieldValue("color", option.value)}
                    sx={{ color: "#fff", cursor: "pointer" }}
                  >
                    {form.values.color === option.value && (
                      <CheckIcon width={16} />
                    )}
                  </ColorSwatch>
                ) : (
                  <div className="relative">
                    <Avatar
                      component="button"
                      type="button"
                      src={option.image}
                      radius="xl"
                      onClick={() => form.setFieldValue("color", option.value)}
                      sx={{ color: "#fff", cursor: "pointer" }}
                    />
                    {form.values.color === option.value && (
                      <div className="absolute inset-0 flex items-center justify-center text-white">
                        <CheckIcon width={16} />
                      </div>
                    )}
                  </div>
                )}
              </Tooltip>
            );
          })}
        </Group>
      </div>
    </>
  );
}
