// ---------------------------------------------
/**
 * Discover page should be a grid of cards displaying
 * art work created by everyone in the platform.
 *
 * Should be searchable by their selections
 * type, words in description, style, color, camera, feels
 */
// ---------------------------------------------

import { ActionIcon, Button, Pagination } from "@mantine/core";
import type { LoaderArgs } from "@remix-run/node";
import { json } from "@remix-run/node";
import { Form, Link, useLoaderData, useSearchParams } from "@remix-run/react";
import { useQueryClient } from "@tanstack/react-query";
import { useEffect, useRef } from "react";
import { ArrowRight, Search, X } from "tabler-icons-react";
import PromptCard from "~/components/PromptCard";
import { PAGE_LIMIT } from "~/lib/const";
import { getAuthSession } from "~/lib/session.server";
import { getSupabaseAdmin } from "~/lib/supabase";
import type { PromptRecord } from "~/modules/prompts";

export async function loader({ request }: LoaderArgs) {
  const authSession = await getAuthSession(request);
  const client = getSupabaseAdmin();

  const url = new URL(request.url);
  const { searchParams } = url;

  const query = client
    .from("prompts")
    .select(
      "*, results:prompt_results!inner(*), like_count:likes(likes), is_liked:prompt_likes(user_id)"
    )
    .not("prompt", "eq", "")
    .eq("approved", true)
    .order("created_at", { ascending: false })
    .limit(PAGE_LIMIT);

  const countQuery = client
    .from("prompts")
    .select("*, results:prompt_results!inner(id)", {
      head: true,
      count: "exact",
    })
    .eq("approved", true)
    .not("prompt", "eq", "");

  if (searchParams.has("q")) {
    query.ilike("prompt", `%${searchParams.get("q")}%`);
    countQuery.ilike("prompt", `%${searchParams.get("q")}%`);
  }

  if (searchParams.has("page")) {
    const page = Number(searchParams.get("page"));
    const offset = page * PAGE_LIMIT;
    query.range(offset - PAGE_LIMIT, offset - 1);
  }

  if (authSession?.userId) {
    query.eq("is_liked.user_id", authSession.userId);
  }

  const { data, error } = await query;
  const { count } = await countQuery;

  if (error) {
    throw new Response("", { status: 500, statusText: error.message });
  }

  return json({
    products: data,
    count,
  });
}

export default function ExplorePage() {
  const queryClient = useQueryClient();
  const { products, count } = useLoaderData<{
    products: PromptRecord[];
    count: any;
  }>();
  const [searchParams, setSearchParams] = useSearchParams();
  const searchRef = useRef<HTMLInputElement>(null);

  const page = Number(searchParams.get("page") ?? 1);

  useEffect(() => {
    products.forEach((prompt) => {
      queryClient.setQueryData(["prompts", prompt.uuid], prompt);
    });
  }, [queryClient, products]);

  return (
    <>
      <section id="hero" className="bg-gray-200">
        <div className="container flex flex-col justify-center py-28 md:py-48 -mt-20">
          <h1 className="text-6xl md:text-8xl font-black tracking-tighter max-w-screen-lg text-gray-800">
            Discover AI Generated Images
          </h1>
          <Form
            id="searchForm"
            method="get"
            className="flex items-center pt-6 pb-12 space-x-4"
          >
            <div className="relative rounded-full border border-gray-200 overflow-hidden flex-1 max-w-md h-12 outline-1 focus-within:outline focus-within:outline-indigo-600">
              <div className="absolute left-0 inset-y-0 flex items-center justify-center w-12 text-gray-400">
                <Search size={24} />
              </div>
              <input
                aria-label="Search"
                type="text"
                name="q"
                defaultValue={searchParams.get("q") ?? ""}
                placeholder="Search Prompts..."
                className="h-full w-full px-12 text-lg rounded-full outline-none"
              />
              <div className="absolute right-0 inset-y-0 flex items-center">
                <ActionIcon
                  size="lg"
                  radius="xl"
                  variant="filled"
                  color="violet"
                  mr={10}
                  type="submit"
                  className="transform will-change-transform transition-transform hover:scale-125"
                >
                  <ArrowRight size={24} />
                </ActionIcon>
              </div>
            </div>

            {[...searchParams.keys()].length > 0 && (
              <Button
                component={Link}
                size="lg"
                radius="xl"
                variant="light"
                leftIcon={<X size={24} />}
                onClick={() => {
                  searchRef.current!.value = "";
                }}
                to="/discover"
              >
                Clear
              </Button>
            )}
          </Form>
        </div>
      </section>

      <section id="product-grid" className="-mt-28 md:-mt-48 mb-24">
        <div className="container grid grid-cols-1 md:grid-cols-3 xl:grid-cols-4 gap-4 lg:gap-8">
          {products.map((prompt) => (
            <PromptCard key={prompt.id} prompt={prompt} asCarousel />
          ))}
        </div>
        <div className="container flex justify-center mt-12">
          <Pagination
            page={page}
            color="violet"
            radius="lg"
            total={Math.ceil(count / PAGE_LIMIT)}
            styles={(theme) => ({
              item: {
                "&[data-active]": {
                  backgroundImage: theme.fn.gradient({
                    from: "violet",
                    to: "pink",
                  }),
                },
              },
            })}
            onChange={(nextPage) => {
              setSearchParams({
                ...searchParams,
                page: `${nextPage}`,
              });
            }}
          />
        </div>
      </section>
    </>
  );
}
