import colors from "tailwindcss/colors";

export interface FormValues {
  prompt: string;
  type: string | null;
  description: string;

  // style
  artist: string[] | null;
  style: string[] | null;
  lighting: string | null;
  detail: string | null;
  techniques: string | null;
  color: string | null;

  // composition
  camera: string | null;
  resolution: string | null;

  // leave
  emotion: string | null;
}

export interface GenericOption {
  label: string;
  value: string;
  image?: string; // future
  options?: GenericOption[];
}

export interface ComplexOption {
  [key: string]: GenericOption[];
}

