const cleanColorKeys = Object.keys(colors).filter(
  (c) =>
    ![
      "inherit",
      "current",
      "transparent",
      "black",
      "white",
      "lightBlue",
      "warmGray",
      "trueGray",
      "coolGray",
      "blueGray",
    ].includes(c)
);

type ColorUnion =
  | { type: "filter"; image: string }
  | { type: "color"; hex: string };

export type ColorOption = GenericOption & ColorUnion;

export const colorTypes: ColorOption[] = [
  {
    label: "Black and White",
    value: "black-white",
    type: "filter",
    image:
      "https://project-ai.sfo3.cdn.digitaloceanspaces.com/static/colors/bw.png",
  },
  {
    label: "Sephia",
    value: "sephia",
    type: "filter",
    image:
      "https://project-ai.sfo3.cdn.digitaloceanspaces.com/static/colors/sephia.png",
  },
  ...cleanColorKeys.map((key) => ({
    label: key,
    value: key,
    type: "color" as const,
    hex: colors[key as keyof typeof colors][800],
  })),
];

export function composePrompt(values: FormValues): string {
  const {
    type,
    description,
    artist,
    style,
    lighting,
    detail,
    techniques,
    color,
    camera,
    resolution,
  } = values;

  let promptString = [];
  if (type) promptString.push(` ${type}`);
  if (description) promptString.push(` ${description}`);

  if (artist) promptString.push(artist.map((a) => ` by ${a}`).join(", "));
  if (style) promptString.push(`, ${style}`);
  if (techniques) promptString.push(`, ${techniques}`);
  if (lighting) promptString.push(`, ${lighting}`);

  if (detail) promptString.push(`, ${detail}`);
  if (color) promptString.push(`, ${color}`);

  if (camera) promptString.push(`, ${camera}`);
  if (resolution) promptString.push(`, ${resolution}`);

  return promptString.join("");
}
