import { useQuery } from "@tanstack/react-query";
import { useAtomValue } from "jotai";

import { accessTokenAtom, userAtom } from "~/lib/atoms/root";
import { getSupabase } from "~/lib/supabase";
import type { PromptRecord } from "./schema";
import { TABLE_NAME, SELECT } from "./const";
import { PAGE_LIMIT } from "~/lib/const";

export async function getPromptCount({
  q,
  accessToken,
}: {
  q?: string | null;
  accessToken: string;
}) {
  const client = getSupabase(accessToken);
  const query = client
    .from(TABLE_NAME)
    .select("*", { head: true, count: "exact" })
    .not("prompt", "eq", "");

  if (q) {
    query.ilike("prompt", `%${q}%`);
  }

  const { count, error } = await query;

  if (error) throw error;

  return count as number;
}

export async function getPrompts({
  q,
  page = 1,
  limit,
  accessToken,
}: {
  q?: string | null;
  page?: number;
  limit?: number;
  accessToken: string;
}) {
  const client = getSupabase(accessToken);
  const query = client
    .from(TABLE_NAME)
    .select(SELECT)
    .not("prompt", "eq", "")
    .order("created_at", { ascending: false });

  const queryLimit = limit ?? PAGE_LIMIT;

  if (q) {
    query.ilike("prompt", `%${q}%`);
  }

  if (page) {
    const cursor = Number(page);
    const offset = cursor * queryLimit;
    query.range(offset - queryLimit, offset - 1);
  }

  query.limit(queryLimit);

  const { data, error } = await query;
  if (error) throw error;

  return data as PromptRecord[];
}

interface GetPromptsRequest {
  initialData: PromptRecord[];
  params: {
    q?: string | null;
    page: number;
    limit: number;
  };
}
export function useGetPrompts(request: GetPromptsRequest) {
  const { initialData, params } = request;
  const accessToken = useAtomValue(accessTokenAtom);
  const user = useAtomValue(userAtom);

  return useQuery({
    queryKey: ["prompts", user.id, params.page, params.q],
    queryFn: () => getPrompts({ accessToken, ...params }),
    initialData,
    keepPreviousData: true,
  });
}

interface GetPromptCountRequest {
  initialData: number;
  params: {
    q?: string | null;
  };
}
export function useGetPromptCount(request: GetPromptCountRequest) {
  const { initialData, params } = request;
  const accessToken = useAtomValue(accessTokenAtom);
  const user = useAtomValue(userAtom);

  return useQuery({
    queryKey: ["prompts-count", user.id, params.q],
    queryFn: () => getPromptCount({ accessToken, ...params }),
    initialData,
  });
}
