import { redirect } from "@remix-run/node";
import type { ActionArgs } from "@remix-run/node";
import {
  commitSession,
  getAuthSession,
  getSession,
} from "~/lib/session.server";
import { getSupabaseAdmin } from "~/lib/supabase";
import { PROMPT_SAVE_KEY } from "~/lib/const";

export async function action({ request, params }: ActionArgs) {
  const { promptId } = params as { promptId: string };
  const authSession = await getAuthSession(request);
  const client = getSupabaseAdmin();

  const { data, error } = await client
    .from("prompts")
    .select("*")
    .match({ uuid: promptId })
    .single();

  if (error) {
    throw new Response("", { status: 500, statusText: "Something went wrong" });
  }

  if (!data) {
    throw new Response("", { status: 404, statusText: "Prompt not found" });
  }

  if (authSession) {
    const { userId } = authSession;
    if (userId === data.created_by) {
      // is the owener
      return redirect(`/app/${data.uuid}`);
    }
  }

  const session = await getSession(request);
  session.set(
    PROMPT_SAVE_KEY,
    JSON.stringify({
      action: "insert",
      ids: [data.id],
    })
  );

  return redirect("/signup", {
    headers: {
      "Set-Cookie": await commitSession(session),
    },
  });
}
