import { Button, Loader, Tooltip } from "@mantine/core";
import { useSearchParams, useTransition } from "@remix-run/react";
import { useMemo } from "react";
import { RectangleVertical, Square } from "tabler-icons-react";
import { useRemoveBg } from "~/lib/hooks/useRemoveBg";
import { useRevalidate } from "~/lib/hooks/useRevalidate";
import Mock from "./Mock";
import { useDesigner } from "./useDesigner";

export default function ProductPreview() {
  const { product, result, printShape } = useDesigner();
  const [searchParams, setSearchParams] = useSearchParams();
  const revalidate = useRevalidate(`/designer`);

  const generateWithoutBg = useRemoveBg();
  const transition = useTransition();
  const printImage = searchParams.get("printImage");
  const removeBg = printImage === "nobgfile";

  const toggleBgPreference = () => {
    if (!result) return;

    if (!result.nobgfile) {
      generateWithoutBg.mutate(
        {
          imageId: result.id,
        },
        {
          onSuccess() {
            revalidate.revalidate();
            // setRemoveBg(true);
            setSearchParams({
              ...Object.fromEntries(searchParams),
              printImage: "nobgfile",
            });
          },
        }
      );
    } else {
      setSearchParams({
        ...Object.fromEntries(searchParams),
        printImage: printImage === "designfile" ? "nobgfile" : "designfile",
      });
      // setRemoveBg((prev) => !prev);
    }
  };

  const imageUrl = useMemo(() => {
    if (!result) return "";
    if (removeBg) {
      return result.nobgfile;
    }
    return result.thumbfile ?? result.filename;
  }, [removeBg, result]);

  return (
    <div className="relative aspect-1 w-full bg-gray-200 rounded-lg shadow-xl overflow-hidden flex items-center justify-center">
      <input
        form="checkoutform"
        type="hidden"
        name="printShape"
        value={printShape}
      />
      <input
        form="checkoutform"
        type="hidden"
        name="printImage"
        value={removeBg ? "nobgfile" : "designfile"}
      />

      {["loading", "submitting"].includes(transition.state) && (
        <Loader size="lg" className="absolute z-10" />
      )}

      <Mock />

      {imageUrl && (
        <>
          {product.product.id !== 2 && (
            <div className="absolute top-2 left-2 md:top-4 md:left-4 z-50">
              <Button.Group>
                <Tooltip label="Square" position="bottom" withinPortal>
                  <Button
                    size="xs"
                    variant={printShape === "square" ? "filled" : "default"}
                    onClick={() => {
                      setSearchParams({
                        ...Object.fromEntries(searchParams),
                        printShape: "square",
                      });
                    }}
                    color="pink"
                  >
                    <Square size={16} />
                  </Button>
                </Tooltip>
                <Tooltip label="Rectangle" position="bottom" withinPortal>
                  <Button
                    size="xs"
                    variant={printShape === "rectangle" ? "filled" : "default"}
                    onClick={() => {
                      setSearchParams({
                        ...Object.fromEntries(searchParams),
                        printShape: "rectangle",
                      });
                    }}
                    color="pink"
                  >
                    <RectangleVertical size={16} />
                  </Button>
                </Tooltip>
              </Button.Group>
            </div>
          )}
          <div className="absolute top-2 right-2 md:top-4 md:right-4 z-50">
            <Button
              size="xs"
              radius="xl"
              variant="filled"
              color="pink"
              // gradient={{ from: "violet", to: "pink" }}
              onClick={() => toggleBgPreference()}
              loading={generateWithoutBg.isLoading}
            >
              {removeBg ? "Revert" : "Remove BG"}
            </Button>
          </div>
        </>
      )}
    </div>
  );
}
