import type { LoaderArgs } from "@remix-run/node";
import { json } from "@remix-run/node";
import { useLoaderData } from "@remix-run/react";
import { Clock } from "tabler-icons-react";
import PromptCard from "~/components/PromptCard";
import { classNames } from "~/lib/classNames";
import { resolveCdn } from "~/lib/resolveUrl";
import { getAuthSession } from "~/lib/session.server";
import { getSupabaseAdmin } from "~/lib/supabase";
import { timeAgo } from "~/lib/timeAgo";
import { CollectionRecord } from "~/modules/collections";
import { AVAILABLE_PRODUCT_IDS, PRODUCT_VARIANTS } from "~/modules/printful";
import type { PromptRecord } from "~/modules/prompts";

export async function loader({ request, params }: LoaderArgs) {
  const authSession = await getAuthSession(request);
  const { slug } = params as { slug: string };
  const client = getSupabaseAdmin();

  const { data: collection } = await client
    .from("collections")
    .select("*")
    .match({ slug })
    .single();

  if (!collection) {
    throw new Response("", {
      status: 404,
      statusText: `Collection ${slug} not found`,
    });
  }

  const query = client
    .from("prompts")
    .select(
      "*, collection:collections!inner(*), results:prompt_results!inner(*), like_count:likes(likes), is_liked:prompt_likes(user_id)"
    )
    .not("prompt", "eq", "")
    .eq("collection.slug", slug);

  if (authSession?.userId) {
    //
    query.eq("is_liked.user_id", authSession.userId);
  }

  const { data, error } = await query;

  console.log(data);
  return json({
    collection: collection,
    prompts: data,
  });
}

