import type { ActionArgs } from "@remix-run/node";
import { redirect } from "@remix-run/node";
import { z } from "zod";
import { resolveCdn, resolveUrl } from "~/lib/resolveUrl";
import { uploadFile } from "~/lib/spaces";
import { createCheckoutSession } from "~/lib/stripe.server";
import { getSupabaseAdmin } from "~/lib/supabase";
import { PRODUCT_RETAIL_PRICING } from "~/modules/printful";

const PurchaseSchema = z.object({
  promptId: z.string().transform((val) => Number(val)),
  variantId: z.string().transform((val) => Number(val)),
  productId: z.string().transform((val) => Number(val)),
  imageId: z.string().transform((val) => Number(val)),
  printImage: z.enum(["nobgfile", "designfile"]),
  printShape: z.enum(["rectangle", "square"]).optional().default("square"),
});

export async function action({ request }: ActionArgs) {
  const formData = Object.fromEntries(await request.formData());
  const validator = PurchaseSchema.safeParse(formData);

  if (!validator.success) {
    throw redirect("/", {
      status: 500,
      statusText: "Missing a required paramter",
    });
  }

  const client = getSupabaseAdmin();

  const { promptId, variantId, imageId, productId, printImage, printShape } =
    validator.data;
  const { data: result } = await client
    .from("prompt_results")
    .select("*, prompt:prompts(*)")
    .match({ id: imageId })
    .single();

  const productNameMap = {
    71: "DAI Men's Staple T-Shirt",
    639: "DAI Women's Relaxed T-Shirt",
    356: "DAI Long Sleeve T-Shirt",
    2: "DAI Matte Paper Framed Poster",
  };
  const productPrice = Number(
    PRODUCT_RETAIL_PRICING[variantId].replace("$", "")
  );
  const productName = productNameMap[productId as keyof typeof productNameMap];

  const url = new URL(request.url);
  const origin = url.origin;
  const hash = Buffer.from(JSON.stringify(validator.data)).toString("base64");
  const success_url = `${origin}/confirmation?hash=${hash}`;
  const cancel_url = `${origin}/designer?promptId=${result.prompt.uuid}&resultId=${result.id}&variantId=${variantId}&productId=${productId}`;

  const printFile =
    printImage === "designfile"
      ? resolveUrl(result.filename)
      : resolveUrl(result.nobgfile);

  const imageData = await fetch(
    `${origin}/api/printful/mock?productId=${productId}&variantId=${variantId}&imageUrl=${printFile}&printShape=${printShape}`
  );
  const buffer = await imageData.arrayBuffer();
  const fileName = [productId, variantId, imageId, printImage].join("-");
  await uploadFile(`stripe/${fileName}.webp`, buffer);

  const paymentLink = await createCheckoutSession({
    line_items: [
      {
        // price: priceId,
        quantity: 1,
        price_data: {
          product_data: {
            name: productName,
            description: `This image will be printed on the ${productName}. Prompt: ${result.prompt.prompt}`,
            images: [resolveCdn(`stripe/${fileName}.webp`)],
          },
          currency: "USD",
          unit_amount: productPrice * 100,
        },
      },
    ],
    success_url,
    cancel_url,
    metadata: {
      promptId,
      variantId,
      imageId,
      printImage,
      printShape,
    },
  });

  return redirect(paymentLink.url!);
}
