// ---------------------------------------------
/**
 * When logged in, /app is the home page for your account
 * Should be able to view your own designs, create new designs and orders
 *
 * Should be dashboard-like
 */
// ---------------------------------------------

import type { LoaderArgs } from "@remix-run/node";
import { json, redirect } from "@remix-run/node";
import { Link, useLoaderData, useSearchParams } from "@remix-run/react";
import { requireAuthSession } from "~/lib/auth/guards.server";
import { getPrompts, getPromptCount } from "~/modules/prompts";
import type { PromptRecord } from "~/modules/prompts";
import { Button, Pagination } from "@mantine/core";
import { commitSession, getSession } from "~/lib/session.server";
import {
  ANON_PROMPT_KEY,
  PAGE_LIMIT,
  PROMPT_SAVE_KEY,
  PROMPT_SESSION_KEY,
} from "~/lib/const";
import { getSupabaseAdmin } from "~/lib/supabase";
import { useEffect } from "react";
import PromptCardOwner from "~/components/PromptCardOwner";

export async function loader({ request }: LoaderArgs) {
  const { userId, accessToken } = await requireAuthSession(request);

  const session = await getSession(request);
  session.unset(PROMPT_SESSION_KEY);

  const promptIdsToSave = session.get(PROMPT_SAVE_KEY);
  if (promptIdsToSave) {
    const { action = "upsert", ids = [] } = JSON.parse(promptIdsToSave);
    const client = getSupabaseAdmin();

    if (action === "upsert") {
      await client.from("prompts").upsert(
        ids.map((pId: string) => ({
          id: Number(pId),
          created_by: userId,
        }))
      );
    } else {
      // duplicate these - usually just one
      const [id] = ids;
      const { data } = await client
        .from("prompts")
        .select("*")
        .match({ id })
        .single();
      if (data) {
        const { data: newPrompt } = await client
          .from("prompts")
          .insert({
            prompt: data.prompt,
            created_by: userId,
          })
          .select("uuid")
          .single();

        session.unset(PROMPT_SAVE_KEY);

        if (newPrompt) {
          return redirect(`/app/${newPrompt.uuid}`, {
            headers: {
              "Set-Cookie": await commitSession(session),
            },
          });
        }
      }
    }
    session.unset(PROMPT_SAVE_KEY);
  }

  const url = new URL(request.url);
  const { searchParams } = url;

  const prompts = await getPrompts({
    accessToken,
    q: searchParams.get("q"),
    limit: PAGE_LIMIT,
    page: Number(searchParams.get("page") ?? 1),
  });
  const count = await getPromptCount({ q: searchParams.get("q"), accessToken });

  return json(
    {
      prompts,
      count,
    },
    {
      headers: {
        "Set-Cookie": await commitSession(session),
      },
    }
  );
}

export default function AppMainPage() {
  const { prompts, count } = useLoaderData<{
    prompts: PromptRecord[];
    count: number;
  }>();
  const [searchParams, setSearchParams] = useSearchParams();

  const page = Number(searchParams.get("page") ?? 1);

  /**
   * edge case to remove local storage
   */
  useEffect(() => {
    if (window.localStorage.getItem(ANON_PROMPT_KEY)) {
      window.localStorage.removeItem(ANON_PROMPT_KEY);
    }
  }, []);

  return (
    <div className="container my-12">
      <section>
        <h3 className="text-6xl font-black tracking-tighter mb-8">
          My Designs ({count})
        </h3>
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-8">
          {prompts.map((prompt) => {
            return <PromptCardOwner key={prompt.id} prompt={prompt} />;
          })}
        </div>

        {prompts.length > 0 && (
          <div className="container flex justify-center mt-12">
            <Pagination
              page={page}
              color="violet"
              radius="lg"
              total={Math.ceil(count / PAGE_LIMIT)}
              styles={(theme) => ({
                item: {
                  "&[data-active]": {
                    backgroundImage: theme.fn.gradient({
                      from: "violet",
                      to: "pink",
                    }),
                  },
                },
              })}
              onChange={(nextPage) => {
                setSearchParams({
                  ...searchParams,
                  page: `${nextPage}`,
                });
              }}
            />
          </div>
        )}
        {prompts.length <= 0 && (
          <div className="h-96 flex flex-col items-center justify-center bg-white rounded-xl shadow-xl">
            <p className="text-4xl md:text-6xl font-black tracking-tighter mb-4">
              You have no designs
            </p>
            <Button
              component={Link}
              variant="gradient"
              gradient={{ from: "violet", to: "pink" }}
              size="lg"
              radius="xl"
              to="/app/create"
            >
              Start Creating
            </Button>
          </div>
        )}
      </section>
    </div>
  );
}
