import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation, Pagination } from "swiper";
import type { Swiper as SwiperType } from "swiper";

import type { PromptRecord } from "~/modules/prompts";
import { useRef } from "react";
import { ChevronLeft, ChevronRight } from "tabler-icons-react";
import { resolveThumbnail } from "~/lib/resolveUrl";
import { Link } from "@remix-run/react";

export default function CardCarousel({
  prompt,
  images,
  navigate,
}: {
  prompt: PromptRecord;
  images: PromptRecord["results"];
  navigate?: string;
}) {
  const swiperRef = useRef<SwiperType>();

  const navigationPrevRef = useRef<HTMLDivElement>(null);
  const navigationNextRef = useRef<HTMLDivElement>(null);

  const shouldSwipe = images && images.length > 1;

  return (
    <Swiper
      slidesPerView={1}
      navigation={
        shouldSwipe
          ? {
              prevEl: navigationPrevRef.current,
              nextEl: navigationNextRef.current,
            }
          : false
      }
      onBeforeInit={(swiper) => {
        swiperRef.current = swiper;
      }}
      pagination={shouldSwipe ? { clickable: true } : false}
      modules={shouldSwipe ? [Navigation, Pagination] : []}
      lazy
    >
      {images?.map((image, index) => {
        return (
          <SwiperSlide key={index}>
            <Link to={navigate ? navigate : `/${prompt.uuid}`}>
              <img
                src={resolveThumbnail(image)}
                alt=""
                width="256"
                height="256"
                className="h-full w-full object-cover"
              />
            </Link>
          </SwiperSlide>
        );
      })}

      {shouldSwipe && (
        <>
          <div
            ref={navigationPrevRef}
            className="cursor-pointer absolute top-1/2 left-0 transform -translate-y-1/2 z-50 h-8 w-8 flex items-center justify-center rounded-tr-md rounded-br-md bg-white"
            onClick={() => {
              swiperRef.current?.slidePrev();
            }}
          >
            <ChevronLeft size={24} />
          </div>
          <div
            ref={navigationNextRef}
            className="cursor-pointer absolute top-1/2 right-0 transform -translate-y-1/2 z-50 h-8 w-8 flex items-center justify-center rounded-tl-md rounded-bl-md bg-white"
            onClick={() => {
              swiperRef.current?.slideNext();
            }}
          >
            <ChevronRight size={24} />
          </div>
        </>
      )}
    </Swiper>
  );
}
