import type { ActionArgs } from "@remix-run/node";
import { json } from "@remix-run/node";
import { RequiredError } from "openai/dist/base";
import { ANON_SESSION_KEY } from "~/lib/const";
import openai from "~/lib/openai.server";
import { getAuthSession, getSession } from "~/lib/session.server";

import { getSupabaseAdmin } from "~/lib/supabase";

export async function action({ request }: ActionArgs) {
  const data = await request.json();
  const authSession = await getAuthSession(request);
  const session = await getSession(request);

  const openAiSessionKey =
    authSession?.userId ?? session.get(ANON_SESSION_KEY)!;

  const { promptId, imageCount = 1 } = data;

  if (!promptId) {
    return json({
      error: "Prompt ID is required",
    });
  }

  const client = getSupabaseAdmin();

  const { data: prompt, error } = await client
    .from("prompts")
    .update({ session_key: openAiSessionKey })
    .match({ id: promptId })
    .select("*")
    .single();

  if (error) {
    return json(
      {
        error: error.message,
      },
      { status: 500 }
    );
  }

  if (!prompt) {
    return json(
      {
        error: "No prompt found",
      },
      { status: 500 }
    );
  }

  try {
    const { data } = await openai.createImage({
      prompt: prompt.prompt as string,
      n: Number(imageCount),
      size: "1024x1024",
      user: openAiSessionKey,
    });

    const { data: results } = await client
      .from("prompt_results")
      .insert(
        data.data.map((image) => ({
          prompt_id: promptId,
          filename: image.url,
        }))
      )
      .select("*");

    return json({
      results,
    });
  } catch (e) {
    console.log("error", e);
    if (e instanceof RequiredError) {
      return json(
        {
          error: e.message,
        },
        { status: 500 }
      );
    }
    return json(
      {
        error: (e as any)?.message ?? "Unknown error",
      },
      { status: 500 }
    );
  }
}

