import { useResizeObserver } from "@mantine/hooks";
import { useEffect, useState } from "react";
import { classNames } from "~/lib/classNames";
import useProductTemplate from "~/lib/hooks/useProductTemplate";
import { resolveCdn } from "~/lib/resolveUrl";
import { useDesigner } from "./useDesigner";

export default function Mock() {
  const { product, variant, result, printShape, printImage } = useDesigner();
  const { data: templates } = useProductTemplate(
    product.product.id,
    variant.id
  );
  const [position, setPosition] = useState<{
    top: number;
    left: number;
    width: number;
    height: number;
  }>({ top: 0, left: 0, width: 0, height: 0 });
  const [ref, bounds] = useResizeObserver<HTMLDivElement>();

  useEffect(() => {
    if (!templates) return;
    if (!ref.current) return;

    const widthRatio = bounds.width / templates.template_width;
    const heightRatio = bounds.height / templates.template_height;

    const printWidth = Math.floor(templates.print_area_width * widthRatio);

    const printHeight =
      printShape === "square"
        ? printWidth
        : Math.floor(templates.print_area_height * heightRatio);

    const offsetY =
      printShape === "square"
        ? (templates.print_area_height * heightRatio - printHeight) / 2
        : (printHeight - printWidth) / 2;

    const offsetTop = (templates.print_area_top + offsetY) * heightRatio;
    const offsetLeft = templates.print_area_left * widthRatio;

    setPosition({
      top: offsetTop,
      left: offsetLeft,
      width: printWidth,
      height: printHeight,
    });
  }, [ref, bounds, templates, printShape]);

  if (!templates) {
    return null;
  }

  return (
    <div ref={ref} className="relative h-full w-full">
      <div
        className="absolute inset-0"
        style={{ background: templates.background_color }}
      ></div>
      <img
        src={templates.image_url}
        className={classNames(
          "absolute inset-0 h-full w-full object-cover",
          templates.is_template_on_front ? "z-20" : "z-10"
        )}
        alt=""
      />
      {result && (
        <img
          src={resolveCdn(
            printImage === "nobgfile" ? result.nobgfile! : result.filename
          )}
          className={classNames(
            "absolute object-cover",
            templates.is_template_on_front ? "z-10" : "z-20"
          )}
          style={position}
          alt=""
        />
      )}
    </div>
  );
}
