import { Button, Textarea } from "@mantine/core";
import { useForm } from "@mantine/form";
import type { LoaderArgs } from "@remix-run/node";
import { json } from "@remix-run/node";
import { Link, useLoaderData, useNavigate } from "@remix-run/react";
import { ShoppingCart } from "tabler-icons-react";
import FastColor from "~/components/FastColor";
import { classNames } from "~/lib/classNames";
import { useGenerateImage } from "~/lib/hooks/useGenerateImage";
import { resolveThumbnail, resolveUrl } from "~/lib/resolveUrl";
import { getAuthSession } from "~/lib/session.server";
import { getSupabaseAdmin } from "~/lib/supabase";
import { useCreatePrompt, useUpdatePrompt } from "~/modules/prompts";
import type { PromptRecord } from "~/modules/prompts";
import Progress from "~/components/Progress";
import PromptHero from "~/components/PromptHero";

export async function loader({ request, params }: LoaderArgs) {
  const authSession = await getAuthSession(request);
  const { promptId } = params as { promptId: string };
  const client = getSupabaseAdmin();

  const query = client
    .from("prompts")
    .select(
      "*, results:prompt_results(*), like_count:likes(likes), is_liked:prompt_likes(user_id)"
    )
    .match({ uuid: promptId });

  if (authSession?.userId) {
    query.eq("is_liked.user_id", authSession.userId);
  }

  const { data, error } = await query.single();

  if (error) {
    throw new Response("", { status: 500, statusText: error.message });
  }

  if (!data) {
    throw new Response("", { status: 404, statusText: "Prompt not found" });
  }

  return json({
    prompt: data,
  });
}

export function action() {
  return { ok: true };
}

export default function PromptEditorPage() {
  const navigate = useNavigate();
  const { prompt } = useLoaderData<{ prompt: PromptRecord }>();

  const createPrompt = useCreatePrompt();
  const updatePrompt = useUpdatePrompt();
  const generateImages = useGenerateImage();

  const form = useForm({
    initialValues: {
      prompt: prompt.prompt,
    },
  });

  const updateAndGenerate = (values: typeof form.values) => {
    createPrompt.mutate(
      {
        prompt: values.prompt,
      },
      {
        onSuccess: (data) => {
          generateImages.mutate(
            {
              promptId: data.id,
              imageCount: 4,
            },
            {
              onSuccess: () => {
                navigate(`/app/${data.uuid}`);
                form.resetDirty();
              },
            }
          );
        },
      }
    );
  };

  const isLoading = updatePrompt.isLoading || generateImages.isLoading;

  return (
    <>
      <section id="hero" className="bg-gray-200 relative">
        {prompt.results && prompt.results.length > 0 && (
          <FastColor
            className="absolute inset-0 z-0"
            image={resolveUrl(prompt.results![0].filename)}
          ></FastColor>
        )}
        <div className="container flex flex-col md:flex-row md:items-center py-20 md:py-24 -mt-20">
          <PromptHero prompt={prompt} />
        </div>
      </section>

      <section className="-mt-12 mb-16 md:mb-32 container relative z-10">
        <div className="bg-white p-12 rounded-lg pb-24">
          <form onSubmit={form.onSubmit(updateAndGenerate)}>
            <Textarea
              label="Prompt"
              size="lg"
              {...form.getInputProps("prompt")}
            />
            <div className="flex flex-col lg:flex-row lg:items-center mt-3 w-full">
              <Button
                variant="gradient"
                gradient={{ from: "violet", to: "pink" }}
                size="lg"
                radius="xl"
                loading={isLoading}
                loaderPosition="right"
                type="submit"
              >
                {form.isDirty() ? "Create New Prompt" : "Generate Images"}
              </Button>
              <div
                className={classNames(
                  "text-slate-600 ml-4 mt-6 lg:mt-0",
                  isLoading ? "block flex-1" : "hidden"
                )}
              >
                <Progress loading={isLoading} />
                <p className="mt-1 text-sm">
                  Generating images may take up to one minute.
                </p>
              </div>
            </div>
          </form>
        </div>

        <div className="px-12 -mt-12 grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4">
          {prompt.results?.map((result, index) => {
            const image = resolveThumbnail(result);
            return (
              <div
                key={index}
                className="relative shadow-xl rounded-lg overflow-hidden backdrop-blur-md"
              >
                <img
                  src={image}
                  alt=""
                  className="aspect-1 w-full object-cover "
                />
                <div className="absolute bottom-4 right-4">
                  <Button
                    component={Link}
                    size="md"
                    variant="gradient"
                    radius="xl"
                    gradient={{ from: "violet", to: "pink" }}
                    rightIcon={<ShoppingCart size={20} />}
                    to={`/designer?promptId=${prompt.uuid}&resultId=${result.id}`}
                    mr={16}
                  >
                    Shop
                  </Button>
                </div>
              </div>
            );
          })}
        </div>
      </section>
    </>
  );
}
