import { FastAverageColor } from "fast-average-color";
import { useEffect, useState } from "react";
import type { ComponentPropsWithoutRef } from "react";
import { useSetAtom } from "jotai";
import { colorSchemeAtom } from "~/lib/atoms/root";

type FastColorProps = ComponentPropsWithoutRef<"div"> & {
  image: string;
};

export default function FastColor({
  image,
  children,
  ...props
}: FastColorProps) {
  const [bg, setBg] = useState<string>();
  const [fg, setFg] = useState<string>();
  const setColorScheme = useSetAtom(colorSchemeAtom);

  useEffect(() => {
    const fac = new FastAverageColor();
    fac.getColorAsync(image).then((color) => {
      setBg(color.rgba);
      setFg(color.isDark ? "#fff" : "#000");
      setColorScheme(color.isDark ? "light" : "dark");
    });

    return () => {
      setColorScheme("dark");
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [image]);

  return (
    <div
      {...props}
      style={{
        background: bg,
        color: fg,
      }}
    >
      {children}
    </div>
  );
}
