import type { ActionArgs } from "@remix-run/node";
import { json } from "@remix-run/node";
import { z } from "zod";

import { createOrder } from "~/lib/printful.server";
import { resizeImage } from "~/lib/resize.server";
import { resolveUrl } from "~/lib/resolveUrl";
import { uploadFile } from "~/lib/spaces";
import { getSupabaseAdmin } from "~/lib/supabase";
import { PRODUCT_PRINTFILES_POSITION } from "~/modules/printful";

const OrderSchema = z.object({
  imageId: z.number(),
  variantId: z.number(),
});

export async function action({ request }: ActionArgs) {
  const payload = await request.json();

  const validation = OrderSchema.safeParse(payload);
  if (!validation.success) {
    return json(
      {
        error: validation.error,
      },
      { status: 500 }
    );
  }

  const client = getSupabaseAdmin();
  const { imageId, variantId } = validation.data;
  const { data, error } = await client
    .from("prompt_results")
    .select("*")
    .match({ id: imageId })
    .single();
  if (error) {
    return json(
      {
        error: error.message,
      },
      { status: 500 }
    );
  }
  if (!data) {
    return json(
      {
        error: "No image found",
      },
      { status: 500 }
    );
  }

  let finalMockImage = data.designfile;
  if (!data.designfile) {
    // sharp
    const image = data.filename.startsWith("http")
      ? data.filename
      : resolveUrl(data.filename);
    const imageBuffer = await resizeImage(image, {
      height: 4000,
      width: 4000,
    });

    const filename = data.filename.split("/").pop();
    const filePath = ["hi-res", `result-${data.id}`, filename].join("/");

    await uploadFile(filePath, imageBuffer);

    await client
      .from("prompt_results")
      .update({
        designfile: filePath,
      })
      .match({ id: data.id });

    finalMockImage = filePath;
  }

  const orderParams = {
    recipient: {
      name: "Franco Valdes",
      address1: "10617 Providence Arbours Dr",
      city: "Charlotte",
      state_code: "NC",
      country_code: "US",
      zip: "28270",
    },
    items: [
      {
        variant_id: variantId,
        quantity: 1,
        files: [
          {
            url: resolveUrl(finalMockImage),
            position: PRODUCT_PRINTFILES_POSITION[variantId],
          },
        ],
      },
    ],
  };

  try {
    const order = await createOrder(orderParams);

    return json(order);
  } catch (error: any) {
    return json(
      {
        error: error.message,
      },
      { status: 500 }
    );
  }
}
