import {
  useGetProductTemplates,
  PRODUCT_PRINTFILES_PLACEMENT,
} from "~/modules/printful";

export default function useProductTemplate(
  productId: number,
  variantId: number
) {
  const { data: templates, isLoading } = useGetProductTemplates(productId, {
    enabled: Boolean(productId),
  });

  if (!templates || !productId) {
    return {
      data: null,
      isLoading,
      error: "No templates found for product/variant",
    };
  }

  const placement = PRODUCT_PRINTFILES_PLACEMENT[productId];

  const variantMap = templates.variant_mapping.find(
    (vm: any) => vm.variant_id === variantId
  );

  if (!variantMap) {
    return {
      data: null,
      isLoading,
    };
  }

  const templateId = variantMap.templates.find(
    (vm: any) => vm.placement === placement
  )?.template_id;

  return {
    data: templates.templates.find((t: any) => t.template_id === templateId),
    isLoading,
  };
}
