import { Group, Button, Stack } from "@mantine/core";
import { useState } from "react";
import type { ReactNode } from "react";
import Modal from "~/components/primitives/Modal";

export function useConfirmationModal() {
  const [opened, setOpened] = useState<boolean>(false);

  const ConfirmationModal = ({
    title,
    onCancel,
    onConfirm,
    children,
  }: {
    title?: ReactNode;
    onCancel?: () => void;
    onConfirm?: () => void;
    children: ReactNode;
  }) => (
    <Modal
      title={title}
      open={opened}
      onOpenChange={() => setOpened(false)}
      withCloseButton={false}
      size="sm"
    >
      <Stack mb={24}>{children}</Stack>
      <Group position="right">
        <Button
          variant="outline"
          radius="lg"
          onClick={() => {
            if (onCancel) {
              onCancel();
            }
            setOpened(false);
          }}
        >
          Cancel
        </Button>
        <Button
          color="red"
          radius="lg"
          onClick={() => {
            if (onConfirm) {
              onConfirm();
            }
            setOpened(false);
          }}
        >
          Confirm
        </Button>
      </Group>
    </Modal>
  );

  return {
    openConfirmModal: () => setOpened(true),
    modal: ConfirmationModal,
  };
}
