import { useQuery } from "@tanstack/react-query";
import type { UseQueryOptions } from "@tanstack/react-query";
import type { ProductTemplate, ProductResponse } from "./schema";

export async function getProduct(id: number) {
  const res = await fetch(`/api/printful/products/${id}`, {
    method: "GET",
  });
  const data = await res.json();
  return data as ProductResponse;
}

export async function getProducts(ids: number[]) {
  return await Promise.all(ids.map(getProduct));
}

export function useGetProduct(
  id: number,
  options: UseQueryOptions<
    ProductResponse,
    unknown,
    ProductResponse,
    Array<string | number>
  > = {}
) {
  return useQuery(["products", id], () => getProduct(id), {
    ...options,
  });
}

export function useGetProducts(
  ids: number[],
  options: UseQueryOptions<
    ProductResponse[],
    unknown,
    ProductResponse[],
    Array<string | number>
  > = {}
) {
  return useQuery(["products", ...ids], () => getProducts(ids), {
    ...options,
    staleTime: Infinity,
  });
}

export async function getProductTemplates(id: number) {
  const res = await fetch(`/api/printful/products/${id}/templates`, {
    method: "GET",
  });
  const data = await res.json();
  return data as ProductTemplate;
}

export function useGetProductTemplates(
  id: number,
  options: UseQueryOptions<
    ProductTemplate,
    unknown,
    ProductTemplate,
    Array<string | number>
  > = {}
) {
  return useQuery(
    ["products", id, "templates"],
    () => getProductTemplates(id),
    {
      ...options,
    }
  );
}
