import { RadioGroup } from "@headlessui/react";
import { Button } from "@mantine/core";
import { useSearchParams, useTransition } from "@remix-run/react";
import { useAtomValue } from "jotai";
import { useMemo } from "react";
import { printfulDataAtom } from "~/lib/atoms/printful";
import { classNames } from "~/lib/classNames";
import { PRODUCT_RETAIL_PRICING } from "~/modules/printful";
import { useDesigner } from "./useDesigner";

export default function ProductDetails() {
  const { product, variant } = useDesigner();
  const transition = useTransition();
  const printfulData = useAtomValue(printfulDataAtom);
  const [searchParams, setSearchParams] = useSearchParams();
  const activeProduct = printfulData.products.find(
    (p) => p.product.id === product.product.id
  );

  const colors = useMemo(() => {
    if (!activeProduct?.variants) return {};
    return activeProduct.variants.reduce((acc, variant) => {
      if (variant.color in acc) {
        return {
          ...acc,
        };
      }
      return {
        ...acc,
        [variant.color]: variant,
      };
    }, {});
  }, [activeProduct]);

  const sizes = useMemo(() => {
    if (!activeProduct?.variants) return {};
    return activeProduct.variants
      .sort((a, b) => a.id - b.id)
      .reduce((acc, variant) => {
        if (variant.size in acc) {
          return {
            ...acc,
          };
        }
        return {
          ...acc,
          [variant.size]: variant,
        };
      }, {});
  }, [activeProduct]);

  const setColorVariant = (color: string) => {
    const newVariant = activeProduct?.variants.find((v) => {
      return v.size === variant.size && v.color_code === color;
    });
    setSearchParams({
      ...Object.fromEntries(searchParams),
      variantId: newVariant!.id.toString(),
    });
  };

  const setSizeVariant = (size: string) => {
    const newVariant = activeProduct?.variants.find((v) => {
      return v.size === size && v.color_code === variant.color_code;
    });
    setSearchParams({
      ...Object.fromEntries(searchParams),
      variantId: newVariant!.id.toString(),
    });
  };

  return (
    <>
      <div className="flex flex-col items-start lg:flex-row lg:justify-between">
        <div className="w-full md:w-1/2 mb-2 md:mb-0">
          <h1 className="text-2xl font-black tracking-tighter text-gray-800 sm:text-3xl">
            {product.product.title.split("|")[0]}
          </h1>
        </div>
        <Button
          size="lg"
          radius="xl"
          variant="gradient"
          gradient={{ from: "violet", to: "pink" }}
          form="checkoutform"
          type="submit"
          loading={transition.state === "submitting"}
        >
          Buy Now {variant && PRODUCT_RETAIL_PRICING[variant.id]}
        </Button>
      </div>

      <div className="mt-4">
        <div className="flex flex-row mb-8 space-x-4">
          <div className="w-1/3 md:w-1/2 mb-4 md:mb-0">
            <h3 className="text-sm font-medium text-gray-800">Color</h3>

            <RadioGroup
              value={variant?.color_code ?? ""}
              onChange={setColorVariant}
              className="mt-2 md:mt-4"
            >
              <RadioGroup.Label className="sr-only">
                {" "}
                Choose a color{" "}
              </RadioGroup.Label>
              <div className="flex items-center space-x-3">
                {Object.values(colors).map((color: any) => (
                  <RadioGroup.Option
                    key={color.color_code}
                    value={color.color_code}
                    className={({ active, checked }) =>
                      classNames(
                        color.selectedClass,
                        active && checked ? "ring ring-offset-1" : "",
                        !active && checked ? "ring-2" : "",
                        "-m-0.5 relative p-0.5 rounded-full flex items-center justify-center cursor-pointer focus:outline-none"
                      )
                    }
                  >
                    <RadioGroup.Label as="span" className="sr-only">
                      {" "}
                      {color.name}{" "}
                    </RadioGroup.Label>
                    <span
                      aria-hidden="true"
                      className={classNames(
                        "h-8 w-8 border border-black border-opacity-10 rounded-full"
                      )}
                      style={{
                        background: color.color_code,
                      }}
                    />
                  </RadioGroup.Option>
                ))}
              </div>
            </RadioGroup>
          </div>

          {/* Sizes */}
          <div className="w-2/3 md:w-1/2">
            <div className="flex items-center justify-between">
              <h3 className="text-sm font-medium text-gray-800">Size</h3>
            </div>

            <RadioGroup
              value={variant?.size ?? ""}
              onChange={setSizeVariant}
              className="mt-2 md:mt-4"
            >
              <RadioGroup.Label className="sr-only">
                {" "}
                Choose a size{" "}
              </RadioGroup.Label>
              <div className="flex flex-wrap items-center">
                {Object.values(sizes).map((size: any) => (
                  <RadioGroup.Option
                    key={size.size}
                    value={size.size}
                    disabled={!size.in_stock}
                    className={({ active }) =>
                      classNames(
                        size.in_stock
                          ? "bg-white shadow-sm text-gray-800 cursor-pointer"
                          : "bg-gray-50 text-gray-200 cursor-not-allowed",
                        active ? "ring-2 ring-indigo-500" : "",
                        "group min-w-[48px] relative border rounded-md mx-px mb-px py-2 px-2 flex items-center justify-center text-sm font-medium uppercase hover:bg-gray-50 focus:outline-none sm:flex-1"
                      )
                    }
                  >
                    {({ active, checked }) => (
                      <>
                        <RadioGroup.Label as="span">
                          {size.size}
                        </RadioGroup.Label>
                        {size.in_stock ? (
                          <span
                            className={classNames(
                              active ? "border" : "border-2",
                              checked
                                ? "border-indigo-500"
                                : "border-transparent",
                              "pointer-events-none absolute -inset-px rounded-md"
                            )}
                            aria-hidden="true"
                          />
                        ) : (
                          <span
                            aria-hidden="true"
                            className="pointer-events-none absolute -inset-px rounded-md border-2 border-gray-200"
                          >
                            <svg
                              className="absolute inset-0 h-full w-full stroke-2 text-gray-200"
                              viewBox="0 0 100 100"
                              preserveAspectRatio="none"
                              stroke="currentColor"
                            >
                              <line
                                x1={0}
                                y1={100}
                                x2={100}
                                y2={0}
                                vectorEffect="non-scaling-stroke"
                              />
                            </svg>
                          </span>
                        )}
                      </>
                    )}
                  </RadioGroup.Option>
                ))}
              </div>
            </RadioGroup>
          </div>
        </div>
        <div className="my-8">
          <div>
            <h3 className="sr-only">Description</h3>

            <div className="space-y-6">
              <p
                className="text-base text-gray-800"
                dangerouslySetInnerHTML={{
                  __html: product.product.description,
                }}
              ></p>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
