import { sendNotification } from "./slack.server";
import { uploadFile } from "./spaces";
import { getSupabaseAdmin } from "./supabase";

export const ENDPOINT = "https://api.printful.com";
export const ENDPOINT_CDN =
  "https://project-ai.sfo3.cdn.digitaloceanspaces.com/printful";

export async function getProduct(id: number) {
  const res = await fetch(`${ENDPOINT}/products/${id}`, {
    method: "GET",
    headers: {
      Authorization: `Bearer ${PRIVATE_PRINTFUL_SECRET}`,
    },
  });
  const data = await res.json();

  const { code, result } = data;
  if (code !== 200) {
    throw new Error(result);
  }

  return result as ProductResponse;
}

