export async function generatePrintfulMocks(
  hiresimage: string
): Promise<{ productId: number; taskId: string }[]> {
  const taskIds = [];

  for (const productId of AVAILABLE_PRODUCT_IDS) {
    //
    const res = await fetch(
      `${ENDPOINT}/mockup-generator/create-task/${productId}`,
      {
        method: "POST",
        headers: {
          Authorization: `Bearer ${PRIVATE_PRINTFUL_SECRET}`,
        },
        body: JSON.stringify({
          variant_ids: PRODUCT_VARIANTS[productId],
          format: "jpg",
          files: [
            {
              placement: PRODUCT_PRINTFILES_PLACEMENT[productId],
              image_url: hiresimage,
              position: PRODUCT_PRINTFILES_POSITION[productId],
            },
          ],
        }),
      }
    );
    const data = await res.json();
    const { code, result } = data;
    if (code !== 200) {
      // log stuff
      taskIds.push({ productId, taskId: result });
      continue;
    }

    taskIds.push({ productId, taskId: result.task_key });
  }

  return taskIds;
}

const PrintfulOrderSchema = z.object({
  recipient: z.object({
    name: z.string(),
    email: z.string().optional(),
    address1: z.string(),
    address2: z.string().optional().nullable(),
    city: z.string(),
    state_code: z.string(),
    country_code: z.string(),
    zip: z.string(),
  }),
  items: z.array(
    z.object({
      variant_id: z.number(),
      quantity: z.number().default(1),
      files: z.array(
        z.object({
          url: z.string(),
          position: z
            .object({
              area_width: z.number().optional(),
              area_height: z.number().optional(),
              width: z.number().optional(),
              height: z.number().optional(),
              top: z.number().optional(),
              left: z.number().optional(),
            })
            .optional(),
        })
      ),
    })
  ),
});

type PrintfulOrder = z.infer<typeof PrintfulOrderSchema>;

export async function createOrder(order: PrintfulOrder) {
  try {
    const res = await fetch(`${ENDPOINT}/orders`, {
      method: "POST",
      headers: {
        Authorization: `Bearer ${PRIVATE_PRINTFUL_SECRET}`,
      },
      body: JSON.stringify(order),
    });
    const data = await res.json();
    const { code, result } = data;
    if (code !== 200) {
      // log stuff
      throw new Error(result);
    }
    return result;
  } catch (error) {
    throw error;
  }
}

export async function createFullOrder(
  customer: ProfileRecord,
  orderData: Order
) {
  const { shipping_address, printful_data } = orderData;
  const { imageId, printImage, variantId } = printful_data;

  const client = getSupabaseAdmin();
  const { data, error } = await client
    .from("prompt_results")
    .select("*")
    .match({ id: imageId })
    .single();

  if (error) {
    throw error;
  }
  if (!data) {
    throw new Error("No image found");
  }

  const filename = data.filename.split("/").pop();
  let designfile = data.designfile;

  if (!data.designfile) {
    const imageBuffer = await resizeImage(resolveUrl(data.filename), {
      height: 4000,
      width: 4000,
    });

    const filePath = ["hi-res", `result-${data.id}`, filename].join("/");

    await uploadFile(filePath, imageBuffer);

    await client
      .from("prompt_results")
      .update({
        designfile: filePath,
      })
      .match({ id: data.id });

    designfile = filePath;
  }

  let nobgfile = data.nobgfile;

  if (printImage === "nobgfile") {
    const imageBuffer = await removeBg(resolveUrl(designfile), "full");
    const hash = new Date().getTime();
    const filePath = [
      "hi-res",
      `result-${data.id}`,
      `nobg-${hash}-${filename}`,
    ].join("/");

    await uploadFile(filePath, imageBuffer);

    await client
      .from("prompt_results")
      .update({
        nobgfile: filePath,
      })
      .match({ id: data.id });
  }

  let finalMockImage = printImage === "nobgfile" ? nobgfile : designfile;

  const orderParams = {
    recipient: {
      name: customer.full_name!,
      email: customer.email,
      address1: shipping_address.line1!,
      address2: shipping_address.line2!,
      city: shipping_address.city!,
      state_code: shipping_address.state!,
      country_code: shipping_address.country!,
      zip: shipping_address.postal_code!,
    },
    items: [
      {
        variant_id: Number(variantId),
        quantity: 1,
        files: [
          {
            url: resolveUrl(finalMockImage),
            position: PRODUCT_PRINTFILES_POSITION[Number(variantId)],
          },
        ],
      },
    ],
  };
  try {
    const order = await createOrder(orderParams);
    return order;
  } catch (error: any) {
    // shh
    // notify
    await sendNotification({
      text: `Order creation failed for ${customer.email}`,
    });
  }
}
