// ---------------------------------------------
/**
 * PromptDetail Page should be the ecommerce product detail
 * page.
 *
 * From here, you can choose the type of gear you want with that design
 *
 */
// ---------------------------------------------
import { Button, Textarea } from "@mantine/core";
import { useForm } from "@mantine/form";
import { Link, useMatches, useNavigate } from "@remix-run/react";
import { ArrowUpRight } from "tabler-icons-react";
import { resolveCdn, resolveThumbnail } from "~/lib/resolveUrl";
import { useCreatePrompt, useUpdatePrompt } from "~/modules/prompts";
import type { PromptResult } from "~/modules/prompts";
import type { PromptRecord } from "~/modules/prompts";
import { useGenerateImage } from "~/lib/hooks/useGenerateImage";
import { useUserData } from "~/lib/hooks/useUserData";
import { useSetAtom } from "jotai";
import { pushPromptAtom } from "~/lib/atoms/prompts";
import { classNames } from "~/lib/classNames";
import Progress from "~/components/Progress";
import { useState } from "react";
import Modal from "~/components/primitives/Modal";
import { event, GTAG_EVENTS } from "~/lib/gtag.client";

export default function PromptDetailPage() {
  // const { prompt } = useOutletContext<{ prompt: PromptRecord }>();
  const { user } = useUserData();
  const setPromptHistory = useSetAtom(pushPromptAtom);
  const prompt: PromptRecord = useMatches().find(
    (m) => m.handle?.outlet === "prompt-detail"
  )?.data?.prompt;

  const navigate = useNavigate();
  const createPrompt = useCreatePrompt();
  const updatePrompt = useUpdatePrompt();
  const generateImages = useGenerateImage();

  const form = useForm({
    initialValues: {
      prompt: prompt.prompt,
    },
  });

  const updateAndGenerate = (values: typeof form.values) => {
    event({
      ...GTAG_EVENTS.CREATE_PROMPT,
      label: "prompt-page",
    });

    createPrompt.mutate(
      {
        prompt: values.prompt,
      },
      {
        onSuccess: (data) => {
          generateImages.mutate(
            {
              promptId: data.id,
              imageCount: 4,
            },
            {
              onSuccess: (genImageData) => {
                if (!user) {
                  //
                  setPromptHistory({
                    ...data,
                    results: [...data.results, ...genImageData.results],
                  });
                }
                navigate(`/${data.uuid}`);
                form.resetDirty();
              },
            }
          );
        },
      }
    );
  };

  const isLoading = updatePrompt.isLoading || generateImages.isLoading;

  return (
    <>
      <section className="-mt-12 mb-16 md:mb-32 container relative z-10">
        <div className="-mt-6 grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4">
          {prompt.results?.map((result, index) => {
            return (
              <ImageCard key={result.id} prompt={prompt} result={result} />
            );
          })}
        </div>

        <div className="bg-white p-6 md:p-12 rounded-lg my-12 shadow-xl max-w-screen-lg mx-auto">
          <form onSubmit={form.onSubmit(updateAndGenerate)}>
            <Textarea
              label="Prompt"
              size="lg"
              minRows={3}
              autosize
              {...form.getInputProps("prompt")}
            />
            <div className="flex flex-col lg:flex-row lg:items-center mt-3 w-full">
              <Button
                variant="gradient"
                gradient={{ from: "violet", to: "pink" }}
                size="lg"
                radius="xl"
                loading={isLoading}
                loaderPosition="right"
                type="submit"
              >
                {form.isDirty() ? "Create New Prompt" : "Generate Images"}
              </Button>
              <div
                className={classNames(
                  "text-slate-600 ml-4 mt-6 lg:mt-0",
                  isLoading ? "block flex-1" : "hidden"
                )}
              >
                <Progress loading={isLoading} />
                <p className="mt-1 text-sm">
                  Generating images may take up to one minute.
                </p>
              </div>
            </div>
          </form>
        </div>
      </section>
    </>
  );
}

function ImageCard({
  prompt,
  result,
}: {
  prompt: PromptRecord;
  result: PromptResult;
}) {
  const [zoom, setZoom] = useState(false);
  return (
    <div className="relative shadow-xl rounded-lg overflow-hidden backdrop-blur-md">
      <img
        src={resolveThumbnail(result)}
        alt=""
        className="aspect-1 w-full object-cover"
        onClick={() => setZoom(true)}
      />
      <div className="absolute bottom-4 right-4">
        <Button
          component={Link}
          size="md"
          variant="gradient"
          radius="xl"
          gradient={{ from: "violet", to: "pink" }}
          rightIcon={<ArrowUpRight size={20} />}
          to={`/designer?promptId=${prompt.uuid}&resultId=${result.id}`}
          mr={16}
        >
          Design
        </Button>
      </div>
      <Modal
        padding={0}
        open={zoom}
        fullScreen
        onOpenChange={() => setZoom(false)}
      >
        <div className="h-full flex items-center justify-center">
          <img
            src={resolveCdn(result.filename)}
            alt=""
            className="aspect-1 w-full object-contain max-w-screen-md"
          />
        </div>
      </Modal>
    </div>
  );
}
