import type { LoaderArgs } from "@remix-run/node";
import sharp from "sharp";
import { z } from "zod";
import { getProductTemplates } from "~/lib/printful.server";
import { resolveUrl } from "~/lib/resolveUrl";
import { PRODUCT_PRINTFILES_PLACEMENT } from "~/modules/printful";

const MockSchema = z.object({
  productId: z.string(),
  variantId: z.string(),
  imageUrl: z.string().optional(),
  printShape: z.enum(["rectangle", "square"]).optional().default("square"),
});

function hexToRgbA(hex: string) {
  var c: any;
  if (/^#([A-Fa-f0-9]{3}){1,2}$/.test(hex)) {
    c = hex.substring(1).split("");
    if (c.length == 3) {
      c = [c[0], c[0], c[1], c[1], c[2], c[2]];
    }
    c = "0x" + c.join("");
    return {
      r: (c >> 16) & 255,
      g: (c >> 8) & 255,
      b: c & 255,
      alpha: 1,
    };
  }
  throw new Error("Bad Hex");
}

async function placeholder() {
  return await sharp({
    create: {
      width: 1024,
      height: 1024,
      channels: 4,
      background: { r: 255, g: 255, b: 255, alpha: 1 },
    },
  })
    .png()
    .toBuffer();
}

export async function loader({ request }: LoaderArgs) {
  const { searchParams } = new URL(request.url);
  const validation = MockSchema.safeParse(Object.fromEntries(searchParams));
  if (!validation.success) {
    return await placeholder();
  }

  const {
    productId,
    variantId,
    imageUrl,
    printShape = "square",
  } = validation.data;

  const forceSquare = Number(productId) === 2;

  const [template] = await Promise.all([
    // getProduct(Number(productId)),
    getProductTemplates(Number(productId)),
  ]);

  // const { data, error } = resultQuery;

  // const selectedVariant = product.variants.find(
  //   (v) => v.id === Number(variantId)
  // )!;
  const selectedTemplateMap = template.variant_mapping.find(
    (vt) => vt.variant_id === Number(variantId)
  )!;
  const templatePlacement = selectedTemplateMap.templates.find(
    (tm) => tm.placement === PRODUCT_PRINTFILES_PLACEMENT[Number(productId)]
  )!;
  const selectedTemplate = template.templates.find(
    (t) => t.template_id === templatePlacement.template_id
  )!;

  // need to be able to send a shirt without an imageUrl
  const printWidth = Math.floor(
    selectedTemplate.print_area_width * (forceSquare ? 1 : 0.95)
  );

  const printHeight =
    printShape === "square"
      ? printWidth
      : Math.floor(
          selectedTemplate.print_area_height * (forceSquare ? 1 : 0.95)
        );

  const offset =
    printShape === "square"
      ? (selectedTemplate.print_area_height - printHeight) / 2
      : (printHeight - printWidth) / 2;
  const offsetX = (selectedTemplate.print_area_width - printWidth) / 2;
  const offsetLeft = selectedTemplate.print_area_left + offsetX;
  const rgba = hexToRgbA(selectedTemplate.background_color);

  const shirt = await fetch(selectedTemplate.image_url);
  const shirtBuff = Buffer.from(await shirt.arrayBuffer());
  const shirtFix = await sharp(shirtBuff)
    .resize(selectedTemplate.template_width, selectedTemplate.template_height)
    .toBuffer();

  const composites: sharp.OverlayOptions[] = [{ input: shirtFix }];

  if (imageUrl) {
    const ai = await fetch(resolveUrl(imageUrl));
    const aiBuff = Buffer.from(await ai.arrayBuffer());
    const aiFix = await sharp(aiBuff)
      .resize(printWidth, printHeight)
      .toBuffer();

    composites.push({
      input: aiFix,
      top: Math.floor(selectedTemplate.print_area_top + offset),
      left: Math.floor(offsetLeft),
    });
  }

  // now build an image
  const image = await sharp({
    create: {
      width: selectedTemplate.template_width,
      height: selectedTemplate.template_height,
      channels: 4,
      background: rgba,
    },
  })
    .composite(composites)
    .withMetadata()
    .webp({ quality: 60 })
    .toBuffer();

  return new Response(image, {
    headers: {
      "Content-type": "image/webp",
    },
  });
}
