import { redirect } from "@remix-run/node";
import sharp from "sharp";

function hexToRgbA(hex) {
  var c;
  if (/^#([A-Fa-f0-9]{3}){1,2}$/.test(hex)) {
    c = hex.substring(1).split("");
    if (c.length == 3) {
      c = [c[0], c[0], c[1], c[1], c[2], c[2]];
    }
    c = "0x" + c.join("");
    return {
      r: (c >> 16) & 255,
      g: (c >> 8) & 255,
      b: c & 255,
      alpha: 1,
    };
  }
  throw new Error("Bad Hex");
}

export async function action() {
  const aiImage =
    "https://project-ai.sfo3.digitaloceanspaces.com/generated/anon/img-OFtYeP9soPqz9gJirpLWp1u6.png";

  const printConfig = {
    template_id: 150560,
    image_url:
      "https://files.cdn.printful.com/m/57-bc3001/medium/ghost/front/05_bc3001_ghost_front_base_whitebg.png?v=1668602552",
    background_url:
      "https://files.cdn.printful.com/m/57-bc3001/medium/bgImages/01_bc3001_heather-black.jpg?v=1668602552",
    background_color: "#0b0b0b",
    printfile_id: 1,
    template_width: 3000,
    template_height: 3000,
    print_area_width: 1010,
    print_area_height: 1346,
    print_area_top: 530,
    print_area_left: 996,
    is_template_on_front: true,
    orientation: "any",
  };

  const shirtImage =
    "https://files.cdn.printful.com/m/57-bc3001/medium/ghost/front/05_bc3001_ghost_front_base_whitebg.png?v=1668602552";

  const [ai, shirt] = await Promise.all([fetch(aiImage), fetch(shirtImage)]);
  const aiBuff = Buffer.from(await ai.arrayBuffer());
  const shirtBuff = Buffer.from(await shirt.arrayBuffer());

  const aiFix = await sharp(aiBuff)
    .resize(printConfig.print_area_width, printConfig.print_area_width)
    .toBuffer();
  const shirtFix = await sharp(shirtBuff)
    .resize(printConfig.template_width, printConfig.template_height)
    .toBuffer();

  const offset =
    (printConfig.print_area_height - printConfig.print_area_width) / 2;

  const rgba = hexToRgbA(printConfig.background_color);

  const image = await sharp({
    create: {
      width: printConfig.template_width,
      height: printConfig.template_height,
      channels: 4,
      background: rgba,
    },
  })
    .composite([
      {
        input: aiFix,
        top: printConfig.print_area_top + offset,
        left: printConfig.print_area_left,
      },
      { input: shirtFix },
    ])
    .withMetadata()
    .png()
    .toBuffer();

  console.log(image);
  return new Response(image, {
    headers: {
      "Content-type": "image/png",
    },
  });
}
